<template>
  <div class="selectTwo" style="position:relative;align:left">
    <el-select style="width:100px" v-model="cityname" placeholder="city" filterable>
      <el-option value="天津" label="天津">天津</el-option>
    </el-select>
    <el-select
      v-model="firstname"
      clearable
      placeholder="department"
      filterable
    >
      <el-option
        v-for="option in firstlist"
        :key="option"
        :label="option"
        :value="option"
      >{{option}}</el-option>
    </el-select>
    <el-select v-model="value" placeholder="请选择套餐">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-select
      v-model="firstname"
      clearable
      placeholder="department"
      filterable
    >
      <el-option
        v-for="option in firstlist"
        :key="option"
        :label="option"
        :value="option"
      >{{option}}</el-option>
    </el-select>
    <el-select v-model="value" placeholder="请选择套餐">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-button type="primary" @click="childMethod">查询</el-button>
  </div>
</template>

<script>
// import Loading from '@/components/loading'
import axios from "axios";
export default {
  name: "selectTwo",
  props: {
    search: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      firstlist: [],
      secondlist: [],
      showsecond: false,
      cityname: "天津",
      firstname: "",
      options: [
        {
          value: "100",
          label: "100"
        },
        {
          value: "200",
          label: "200"
        },
        {
          value: "300",
          label: "300"
        },
        {
          value: "500",
          label: "500"
        }
      ],
      value: ""
    };
  },
  watch: {
    firstname(val) {
      this.$store.dispatch("getNewDepartment", val);
    },
    value(val) {
      this.$store.dispatch("getNewMeal", val);
    }
  },
  methods: {
    getfirst() {
      axios
        .get("/api/menus/department/")
        .then(response => {
          this.firstlist = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      this.firstname = this.$store.getters.getDepartment;
    },
    childMethod() {
      if (this.search) {
        this.search();
      }
    }
  },
  created() {
    this.getfirst();
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



