<template>
  <div>
    <selectDep :search="search"></selectDep>
    <div class="diffWrapper" v-loading="loading">
      <div class="LRWrapper">
        <div id="left-line-chart" class="line-chart"></div>
        <div id="left-bar-chart" class="bar-chart"></div>
      </div>
      <div class="LRWrapper">
        <div id="right-line-chart" class="line-chart"></div>
        <div id="right-bar-chart" class="bar-chart"></div>
      </div>
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="type" label="类型" width="180" fixed></el-table-column>
      <el-table-column prop="_200Meal" label="200M" ></el-table-column>
      <el-table-column prop="_300Meal" label="300M" ></el-table-column>
      <el-table-column prop="_500Meal" label="500M" ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import selectDep from "@/components/basic/selectDep";
import echarts from "echarts";
import axios from "axios";

export default {
  name: "PonFlowSpeed",
  data() {
    return {
      list: [],
      loading: false,
      _200Meal: {avg: [], peak: [], avg_mean: -1, peak_mean: -1, date: []},
      _300Meal: {avg: [], peak: [], avg_mean: -1, peak_mean: -1, date: []},
      _500Meal: {avg: [], peak: [], avg_mean: -1, peak_mean: -1, date: []},
      tableData: []
    };
  },
  components: {
    selectDep
  },
  methods: {
    initChart(domName) {
      let myChart = echarts.init(document.getElementById(domName));

      let categoryData = [];
      let valueData = [];
      let dataCount = 500;
      let data = generateData(dataCount);

      let option = {
        title: {
          text: echarts.format.addCommas(dataCount) + " Data",
          left: 10
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: false
            },
            saveAsImage: {
              pixelRatio: 2
            }
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          bottom: 90
        },
        dataZoom: [
          {
            type: "inside"
          },
          {
            type: "slider"
          }
        ],
        xAxis: {
          data: data.categoryData,
          silent: false,
          splitLine: {
            show: false
          },
          splitArea: {
            show: false
          }
        },
        yAxis: {
          splitArea: {
            show: false
          }
        },
        series: [
          {
            type: "bar",
            data: data.valueData,
            // Set `large` for large data amount
            large: true
          }
        ]
      };

      function generateData(count) {
        for (let i = 0; i < count; i++) {
          categoryData.push(i);
          valueData.push(Math.random() * 50);
        }

        return {
          categoryData: categoryData,
          valueData: valueData
        };
      }
      myChart.setOption(option);
    },
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      if(dep === '') {
        // 选中全天津
        dep = '0'
      }
      // console.log(`/api/${dep}/${meal}/history/`);
      Promise.all([
        axios({
          method: "get",
          url: `/api/${dep}/200M/history/`
        }),
        axios({
          method: "get",
          url: `/api/${dep}/300M/history/`
        }),
        axios({
          method: "get",
          url: `/api/${dep}/500M/history/`
        }),
      ])
      .then((res) => {
        let result = res;
        console.log(result)
        let mealData_200 = res[0].data.data
        this._200Meal = {
          avg: mealData_200.avg,
          peak: mealData_200.peak,
          date: mealData_200.date,
          avg_mean: mealData_200.avg_mean,
          peak_mean: mealData_200.peak_mean
        }
        let mealData_300 = res[1].data.data
        this._300Meal = {
          avg: mealData_300.avg,
          peak: mealData_300.peak,
          date: mealData_300.date,
          avg_mean: mealData_300.avg_mean,
          peak_mean: mealData_300.peak_mean
        }
        let mealData_500 = res[2].data.data
        this._500Meal = {
          avg: mealData_500.avg,
          peak: mealData_500.peak,
          date: mealData_500.date,
          avg_mean: mealData_500.avg_mean,
          peak_mean: mealData_500.peak_mean
        }
      })
      .then(() => {
        this.initLineChart("left-line-chart",this._200Meal,this._300Meal,this._500Meal, 'avg');
        this.initLineChart("right-line-chart",this._200Meal,this._300Meal,this._500Meal, 'peak');
        this.initBarChart("left-bar-chart",this._200Meal,this._300Meal,this._500Meal, 'avg');
        this.initBarChart("right-bar-chart",this._200Meal,this._300Meal,this._500Meal, 'peak');
        let avg = {type: '平均', _200Meal: this._200Meal.avg_mean, _300Meal: this._300Meal.avg_mean, _500Meal: this._500Meal.avg_mean}
        let peak = {type: '峰值', _200Meal: this._200Meal.peak_mean, _300Meal: this._300Meal.peak_mean, _500Meal: this._500Meal.peak_mean}
        this.tableData = []
        this.tableData.push(avg)
        this.tableData.push(peak)
        this.loading = false;
      })
    },
    initLineChart(domName, _200Meal, _300Meal, _500Meal, type) {
      let myChart = echarts.init(document.getElementById(domName));
      let data = {}
      if(type === 'avg') {
        data._200Meal = _200Meal.avg
        data._300Meal = _300Meal.avg
        data._500Meal = _500Meal.avg
        data.date = _200Meal.date
        data.title = '均值'
        data.markPoint = [_500Meal.date[19],_500Meal.avg[19]]
      } else {
        data._200Meal = _200Meal.peak
        data._300Meal = _300Meal.peak
        data._500Meal = _500Meal.peak
        data.date = _200Meal.date
        data.title = '峰值'
        data.markPoint = [_500Meal.date[19],_500Meal.peak[19]]
      }
      let option = {
        title: {
          text: data.title
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["200M", "300M", "500M"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data:  data.date
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "200M",
            type: "line",
            data:  data._200Meal
          },
          {
            name: "300M",
            type: "line",
            data:  data._300Meal
          },
          {
            name: "500M",
            type: "line",
            data:  data._500Meal,
            markPoint: {
              symbol: 'pin',
              data: [
                {
                  name: '春节',
                  coord: data.markPoint
                  // type: 'max'
                }
              ],
              label: {
                show: 'true',
                formatter: '春节'
              }
            }
          },
        ]
      };
      myChart.setOption(option);
    },
    initBarChart(domName,  _200Meal, _300Meal, _500Meal, type) {
      let myChart = echarts.init(document.getElementById(domName));
      let data = {}
      if(type === 'avg') {
        data._200Meal = _200Meal.avg_mean
        data._300Meal = _300Meal.avg_mean
        data._500Meal = _500Meal.avg_mean
        data.date = _200Meal.date
        data.title = '均值'
      } else {
        data._200Meal = _200Meal.peak_mean
        data._300Meal = _300Meal.peak_mean
        data._500Meal = _500Meal.peak_mean
        data.date = _200Meal.date
        data.title = '峰值'
      }
      let option = {
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ["product", "200M", "300M", "500M"],
            [data.title, data._200Meal, data._300Meal, data._500Meal]
          ]
        },
        xAxis: { type: "category" },
        yAxis: {},
        series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }]
      };
      myChart.setOption(option);
    }
  }
};
</script>

<style scoped lang="stylus">
.diffWrapper {
  display: flex;
  justify-content: space-around;
  height: 800px;
  width: 1200px;
  // background-color: #FF6666;
  padding: 30px;
  margin-top: 20px;

  .LRWrapper {
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    justify-content: space-around;

    // background-color: #FFFF00;
    .line-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }

    .bar-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }
  }
}
</style>



