<template>
<div class="app-container">
        <div class="app-side app-side-left">
            <sidebar :data="sidebardata" :open="defultopen"></sidebar>
        </div>
        <div class='app-right' style="overflow-y: auto; position:relative">
            <router-view name='appRight'></router-view>
        </div>
</div>
</template>

<script >
import sidebar from '@/components/sidebar'
export default {
    data(){
        return{
        defultopen:["/trafficAnalysis","/traffic"],
        sidebardata:{
            name:"用户流量分析",
            path:'/trafficAnalysis',
            children:[{
                name:"套餐流量分析",
                path:"/traffic",
                children:[{
                name:"人均上行流量",
                path:"/upstreamTraffic"
                },
                {
                name:"人均下行流量",
                path:"/downstreamTraffic"
                },
                {
                name:"上下行流量的比例",
                path:"/averageTrafficRate"
                },
                {
                name:"不同套餐的平均上行流量",
                path:"/upstreamTrafficPerPackage"
                },
                {
                name:"不同套餐的平均下行流量",
                path:"/downstreamTrafficPerPackage"
                }]
            },{
                name:"ONU用户流量分析",
                path:"/ONUtraffic",
                children:[{
                name:"不匹配用户的平均上行流量",
                path:"/upstreamTrafficNotMatch"
                },
                {
                name:"不匹配用户的平均下行流量",
                path:"/downstreamTrafficNotMatch"
                },
                {
                name:"匹配用户的平均上行流量",
                path:"/upstreamTrafficMatch"
                },
                {
                name:"匹配用户的平均下行流量",
                path:"/downstreamTrafficMatch"
                }]
            },{
              name:"流量时间趋势分析",
              path:"/trafficTrends",
              children:[{
                name:"人均上行流量变化趋势",
                path:"/upstreamTrafficTrends"
                },
                {
                name:"人均下行流量变化趋势",
                path:"/downstreamTrafficTrends"
                }]
            }]
            },
        }
    },
    components:{
        sidebar
    }

}
</script>

<style>
</style>



