<template>
  <div>
    <selectThree :search="search"></selectThree>
    <el-select v-model="traffic_tag" placeholder="请选择">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <router-link to="/networkResources/userNeed/TrendDiff"><el-button type="danger"  v-show="dangerVisible">发现跳变</el-button></router-link>
    <!-- <button @click="search"></button> -->
    <div class="trendCountWrapper">
      <div id="barChart" class="barChart"></div>
      <div id="pieChart" class="pieChart"></div>
    </div>
    <div class="TrendWrapper" v-loading="loading">
      <div class="increaseWrapper">
        <h1 v-show="titleShow">流量增长</h1>
        <div class="increaseItemWrapper">
          <div
            class="item"
            v-for="(n,index) in increaseCount"
            :key="n"
            :id="setIncreaseId(index)"
          >{{n}}</div>
        </div>
      </div>
      <div class="stableWrapper">
        <h1 v-show="titleShow">流量稳定</h1>
        <div class="stableItemWrapper">
          <div class="item" v-for="(n,index) in stableCount" :key="n" :id="setStableId(index)" @click="btnClick(index)">{{n}}</div>
        </div>
      </div>
      <div class="decreaseWrapper">
        <h1 v-show="titleShow">流量下降</h1>
        <div class="decreaseItemWrapper">
          <div class="item" v-for="(n,index) in decreaseCount" :key="n" :id="setDecreaseId(index)">{{n}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import selectThree from "@/components/basic/selectThree";
import echarts from "echarts";
import axios from "axios";

export default {
  name: "PonFlowTrend",
  data() {
    return {
      list: [],
      viewHeight: 0,
      loading: false,
      titleShow: false,
      stableCount: 0,
      decreaseCount: 0,
      increaseCount: 0,
      stableData: [],
      decreaseData: [],
      increaseData: [],
      diffData: [],
      increaseChartData: {},
      stableChartData: {},
      decreaseChartData: {},
      traffic_tag: 'output',
      dangerVisible: false,
      options: [{
          value: 'input',
          label: '上行'
        }, {
          value: 'output',
          label: '下行'
        }],
    };
  },
  components: {
    selectThree
  },
  methods: {
    setIncreaseId(index) {
      return "increase" + index;
    },
    setStableId(index) {
      return "stable" + index
    },
    setDecreaseId(index) {
      return "decrease" + index
    },
    btnClick(e) {
      console.log(this.stableChartData[`stable${e}`].title)
    },
    initChart(domName, xData, avgData, peakData, trendLineData, title) {
      let myChart = echarts.init(document.getElementById(domName));
      let option = {
        title: {
          text: title,
          textStyle: {
            fontSize: 15
          }
        },
        tooltip: {
          show: true,
          position: 'right'
        },
        color: ["#0099FF", "#99CC00",'#F00000'],
        backgroundColor: '#fff',
        xAxis: {
          data: xData,
          axisLabel: {
            interval: 8,
            rotate: 45,
            fontSize: 8,
          },
        },
        yAxis: {},
        grid: {
          // top: 10,
          bottom: 45,
          left: 30,
        },
        series: [
          {
            symbolSize: 5,
            type: "scatter",
            data: avgData,
            label: {
              // show: true
            }
          },
          {
            symbolSize: 5,
            data: peakData,
            type: "scatter",
          },
          {
            symbolSize: 10,
            data: trendLineData,
            type: "line",
          }
        ]
      };

      myChart.setOption(option);
      // window.addEventListener("resize", function() {
      //   myChart.resize();
      // });
    },
    initBarChart(stableCount,decreaseCount,increaseCount) {
      let myChart = echarts.init(document.getElementById('barChart'));
      let option = {
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ["product", "增加","平稳", "减少"],
            ["数量", increaseCount, stableCount, decreaseCount]
          ]
        },
        xAxis: { type: "category" },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }]
      };
      myChart.setOption(option);
    },
    initPieChart(stableCount,decreaseCount,increaseCount) {
      let myChart = echarts.init(document.getElementById('pieChart'));
      let option = {
        title : {
            text: '平稳增长减少比例',
            subtext: '',
            x:'center'
        },
        tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
            orient: 'vertical',
            left: 'left',
            data: ['增长','平稳','减少']
        },
        series : [
            {
                name: '访问来源',
                type: 'pie',
                radius : '55%',
                center: ['50%', '60%'],
                data:[
                    {value:increaseCount, name:'增长'},
                    {value:stableCount, name:'平稳'},
                    {value:decreaseCount, name:'减少'},
                ],
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
    }
    myChart.setOption(option);
    },
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let station = this.$store.getters.getStation;
      let olt = this.$store.getters.getOlt
      // let traffic_tag = this.$store.getters.getTrafficTag
      let traffic_tag = this.traffic_tag
      axios({
        method: "get",
        url: "/api/aaa/pon-port/trend/",
        params: {
          department_name: dep,
          city_name: '天津',
          station: station,
          olt_name: olt,
          tag: traffic_tag
        }
      })
        .then(res => {
          let data = res.data.data;
          this.stableCount = 0;
          this.decreaseCount = 0;
          this.increaseCount = 0;
          this.stableData = [];
          this.decreaseData = [];
          this.increaseData = [];
          this.diffData = [];
          data.map((item, index) => {
            if(!item.y_diff) {
              this.diffData.push(item)
            }
            switch (item.type) {
              case "STABLE":
                this.stableCount++;
                item.id = index;
                this.stableData.push(item);
                break;
              case "DECREASE":
                this.decreaseCount++;
                item.id = index;
                this.decreaseData.push(item);
                break;
              case "INCREASE":
                this.increaseCount++;
                item.id = index;
                this.increaseData.push(item);
                break;
              default:
                break;
            }
          });
        })
        .then(() => {
          this.titleShow = true
          let val = this.diffData
          if(val.length > 0){
            this.$store.dispatch("getNewDiffData", val);
            this.dangerVisible = true
          }
          // console.log(this.stableData, this.decreaseCount, this.increaseCount);
          this.initBarChart(this.stableCount,this.decreaseCount,this.increaseCount)
          this.initPieChart(this.stableCount,this.decreaseCount,this.increaseCount)
          this.dataPreProcess(this.increaseData,'increase',this.increaseChartData)
          this.dataPreProcess(this.stableData,'stable',this.stableChartData)
          this.dataPreProcess(this.decreaseData,'decrease',this.decreaseChartData)
            this.loading = false
            this.lazyload()
            this.addScroll()
          });
    },
    addScroll() {
      let dom = document.querySelector('.TrendWrapper')
      dom.addEventListener('scroll', this.throttling(this.lazyload,100, 1000))
    },
    throttling(fn, wait, maxTimelong) {
      // 节流函数
      let timeout = null
      let startTime = +new Date()
      return function () {
        if (timeout !== null) clearTimeout(timeout);
        let curTime = +new Date()
        if (curTime - startTime >= maxTimelong) {
          fn()
          startTime = curTime
        } else {
          timeout = setTimeout(fn, wait)
        }
      }
    },
    lazyload() {
      let ele = document.querySelectorAll('.item')
      console.log(this.viewHeight)
      Array.prototype.forEach.call(ele, (item, index) => {
        let rect
        if (item.dataset.chartLazy === '') {
          return
        }
        rect = item.getBoundingClientRect()

        if (rect.bottom > 0 && rect.top < this.viewHeight) {
          // console.log(item.id[0])
          let data
          switch (item.id[0]) {
            case 'i':
              data = this.increaseChartData[item.id]
              break;
            case 's':
              data = this.stableChartData[item.id]
              break;
            case 'd':
              data = this.decreaseChartData[item.id]
              break;
            default:
              break;
          }
          this.initChart(data.name, data.xData, data.avgData, data.peakData, data.trendLineData,data.title)
        }
      })
    },
    dataPreProcess(data,datatype,dataDes) {
      data.map((item, index) => {
      let name = `${datatype}${index}`
      dataDes[name] = {}
      let that = dataDes[name]
      that.name = name
      that.xData = item.samples.x
      that.avgData = item.samples.avg
      that.peakData = item.samples.peak
      let tl = item.trend_line
      that.tl = item.trend_line
      that.trendLineData = [[tl.x1,tl.y1],[tl.x2,tl.y2]]
      that.title = `${item.olt}--${item.pon_board}--${item.pon_port}`
      })
    }
  },
    mounted() {
      this.viewHeight = document.documentElement.clientHeight
    },
};
</script>

<style scoped lang="stylus">
.TrendWrapper {
  display: flex;
  justify-content: space-between;
  height: 550px;
  overflow: auto;
  .increaseWrapper {
    width: 410px;
    // background: #CCCC33;

    .increaseItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .item {
        width: 200px;
        height: 250px;
        background: #D6DBDF;
        margin: 5px 0;
      }
    }
  }

  .stableWrapper {
    width: 410px;
    // background: #FFFF99;

    .stableItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      border-right: 5px solid #3498DB
      border-left: 5px solid #3498DB
    }

    .item {
      width: 200px;
      height: 250px;
      background: #D6DBDF;
      margin: 5px 0;
    }
  }

  .decreaseWrapper {
    width: 410px;
    // background: #CC9933;

    .decreaseItemWrapper {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    .item {
      width: 200px;
      height: 250px;
      background: #D6DBDF;
      margin: 5px 0;
    }
  }
}
.trendCountWrapper
  display: flex
  justify-content: space-around
  margin-bottom: 30px
  .barChart
    width: 550px
    height: 300px
    // background-color: #F1C40F
  .pieChart
    width: 550px
    height: 300px
    // background-color: #F1C40F

h1
  text-align center
</style>



