<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true">新建</el-button>

    <el-dialog title="设置参数" :visible.sync="dialogFormVisible">
      <div>
        类型：
        <el-input v-model="newType" placeholder="请输入类型" size="medium" class="elInput"></el-input>分辨率：
        <el-input v-model="newBandwidth" placeholder="请输入带宽" class="elInput"></el-input>
        <br /> 帧率：
        <el-input v-model="newTime_delay" placeholder="请输入帧率" class="elInput"></el-input>颜色位数：
        <el-input v-model="newPacket_loss_rate" placeholder="请输入颜色位数" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改参数" :visible.sync="dialogForm2Visible">
      <div>
        类型：
        <el-input v-model="newType" placeholder="请输入类型" size="medium" class="elInput"></el-input>分辨率：
        <el-input v-model="newBandwidth" placeholder="请输入带宽" class="elInput"></el-input>
        <br />帧率：
        <el-input v-model="newTime_delay" placeholder="请输入帧率" class="elInput"></el-input>颜色位数：
        <el-input v-model="newPacket_loss_rate" placeholder="请输入颜色位数" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false">取 消</el-button>
        <el-button type="primary" @click="updateNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="type" label="类型" width="180"></el-table-column>
      <el-table-column prop="bandwidth" label="带宽" width="180"></el-table-column>
      <el-table-column prop="time_delay" label="时延" width="180"></el-table-column>
      <el-table-column prop="Packet_loss_rate" label="丢包率" width="180"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small">删除</el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [
        {
          type: "点播业务不考虑云端优化",
          bandwidth: '≥ 25Mb/s',
          time_delay: '≤ 20ms',
          Packet_loss_rate: '≤ 3.4*10-5',
          id: 1
        }
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newType: "",
      newBandwidth: "",
      newTime_delay: "",
      newPacket_loss_rate: "",
      formLabelWidth: "",
      operationId: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFrom4K/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      axios({
        method: "get",
        url: "/api/insertInto4K/",
        params: {
          type: this.newType,
          bandwidth: this.newBandwidth,
          time_delay: this.newTime_delay,
          Packet_loss_rate: this.newPacket_loss_rate,
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data
          alert("新建成功");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id;
      this.newType = e.type,
      this.newBandwidth = e.bandwidth,
      this.newTime_delay = e.time_delay,
      this.newPacket_loss_rate = e.Packet_loss_rate,
      this.dialogForm2Visible = true
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/updateFrom4K/",
        params: {
          id: this.operationId,
          type: this.newType,
          bandwidth: this.newBandwidth,
          time_delay: this.newTime_delay,
          Packet_loss_rate: this.newPacket_loss_rate,
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert('修改成功')
          this.dialogForm2Visible = false;
        } else {
          alert('修改失败')
          this.dialogForm2Visible = false;
        }
      })
    },
    del_row(e) {
      if(confirm('确认要删除吗？')) {
        axios({
          method: "get",
          url: "/api/deleteFrom4K/",
          params: {
            id: e.id
          }
        }).then(res => {
          if(res.data.success) {
            this.tableData = res.data.data;
            alert('删除成功')
          }
        })
      }
    }
  }
};``
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




