<template>
    <div class="app-container">
        <div class="app-side app-side-left">
            <sidebar :data="sidebardata" :open="defultopen"></sidebar>
        </div>
        <div class='app-right' style="overflow-y: auto; position:relative">
            <router-view name='appRight'></router-view>
        </div>
    </div>
</template>

<script>
import sidebar from '@/components/sidebar'
export default {
    data(){
        return{
        defultopen:["/networkResource","/olt"],
        sidebardata:{
            name:"网络资源统计",
            path:'/networkResource',
            children:[{
                name:"OLT分析",
                path:"/olt",
                children:[{
                name:"OLT数量及各厂家占比",
                path:"/vendorCount"
                },
                {
                name:"各型号占比",
                path:"/modelCount"
                },
                {
                name:"OLT下挂用户分布",
                path:"/oltUsersCount"
                },
                {
                name:"分业务带宽的用户数分布",
                path:"/usersStatA"
                },
                {
                name:"IPTV用户数分布",
                path:"/usersStatIPTV"
                },
                {
                name:"分业务类型的用户数分布",
                path:"/usersStat"
                }]
            },{
                name:"PON分析",
                path:"/pon",
                children:[{
                name:"PON口数量及利用率",
                path:"/ponCount"
                },
                {
                name:"业务板槽位利用率分布",
                path:"/serviceBoardStat"
                },
                {
                name:"PON口制式比例",
                path:"/ponType"
                },
                {
                name:"PON口制式厂家比例",
                path:"/vendorPonType"
                },
                {
                name:"PON口流量利用率",
                path:"/trafficStat"
                }]
            },{
                name:"上联分析",
                path:"/upper",
                children:[{
                name:"100M与GE与10GE端口数",
                path:"/portCount"
                },
                {
                name:"上联口流量分布",
                path:"/trafficRate"
                }],
            },{
                name:"设备分析",
                path:"/onu",
                children:[{
                name:"三种ONU的比例",
                path:"/ONUType"
                },
                {
                name:"ONU不匹配的用户数",
                path:"/ONUnotMatch"
                }]
            },
            {
                name: "流量分析",
                path: "/userNeed",
                children:[{
                  name: "高流量用户挖掘",
                  path: "/networkResources/userNeed/flowDistrobution"
                },{
                  name: "高流量用户套餐",
                  path: "/networkResources/userNeed/HighUserMeal"
                },{
                  name: "不同套餐消耗的PON口速率",
                  path: "/networkResources/userNeed/DiffCombPonSpeed"
                },{
                  name: "套餐对比",
                  path: "/networkResources/userNeed/Mealcomparison"
                },{
                  name: "PON口速率分布",
                  path: "/networkResources/userNeed/PonFlowSpeed"
                },{
                  name: "PON口流量速率发展趋势",
                  path: "/networkResources/userNeed/PonFlowTrend"
                }]
            }
            ]
            },
        }
    },
    components:{
        sidebar
    }

}
</script>

<style>
.app-container{
position: relative;
padding:0;
margin:0;
height: calc(100% - 95px);
width:100%;
display: flex;
}
.app-side {
    height: 100%;
    width:250px;
    font-weight: 700;
    color:#333;
    background-color: #fff;
    /* border-right: solid 1px grey; */
    overflow-y: scroll;

}
.app-right {
    width: calc(100% - 230px);
    height: calc(100% - 40px);
    padding:20px;
    overflow-y: scroll;
    overflow: auto
}
</style>



