<template>
  <div>
    <selectOne :search="search"></selectOne>
    <div class="block">
      <span class="demonstration">日期选择</span>
      <el-date-picker v-model="date" type="date" placeholder="选择日期"></el-date-picker>
    </div>
    <div>
      <div id="chartWrapper1" v-loading="loading" style="height:600px;width:100%;margin-top:30px"></div>
      <div id="chartWrapper2" v-loading="loading" style="height:600px;width:100%;margin-top:30px"></div>
      <div id="chartWrapper3" v-loading="loading" style="height:600px;width:100%;margin-top:30px"></div>
      <div id="chartWrapper4" v-loading="loading" style="height:600px;width:100%;margin-top:30px"></div>
    </div>
  </div>
</template>

<script>
import selectOne from "@/components/basic/selectOne";
import echarts from "echarts";
import axios from "axios";
export default {
  name: "PonFlowSpeed",
  data() {
    return {
      list: [],
      loading: false,
      out_peak: {},
      out_avg: {},
      in_peak: {},
      in_avg: {},
      date: '',
    };
  },
  components: {
    selectOne
  },
  methods: {
    initChart(domName, data, title) {
      let myChart = echarts.init(document.getElementById(domName));
      myChart.clear();
      let option = {
        title: {
          text: title,
          left: 10
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: false
            },
            saveAsImage: {
              pixelRatio: 2
            }
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          bottom: 90
        },
        dataZoom: [
          {
            type: "inside"
          },
          {
            type: "slider"
          }
        ],
        xAxis: {
          data: data.x,
          name: "(MB/s)",
          silent: false,
          splitLine: {
            show: false
          },
          splitArea: {
            show: false
          }
        },
        yAxis: {
          splitArea: {
            show: false
          },
          name: "端口数量"
        },
        series: [
          {
            type: "bar",
            data: data.y,
            // Set `large` for large data amount
            large: true
          }
        ]
      };

      myChart.setOption(option);
      window.addEventListener("resize", function() {
        myChart.resize();
      });
    },
    search() {
      let dep = this.$store.getters.getDepartment;
      let value = this.$store.getters.getValue;
      this.loading = true
      axios({
        method: "get",
        url: "/api/aaa/pon-statistics/",
        params: {
          city_name: "天津",
          department_name: dep,
          interval: value
        }
      })
        .then(res => {
          let data = res.data.data;
          console.log(data);
          if ((data.success = true)) {
            this.out_peak = data.out_peak;
            this.in_peak = data.in_peak;
            this.out_avg = data.out_avg;
            this.in_avg = data.in_avg;
          }
          console.log('data',data.out_peak.y)
        })
        .then(() => {
          this.initChart(
            "chartWrapper1",
            this.out_peak,
            "PON端口流量下行峰值速率分布"
          );
          this.initChart(
            "chartWrapper2",
            this.out_avg,
            "PON端口流下行均值速率分布"
          );
          this.initChart(
            "chartWrapper3",
            this.in_peak,
            "PON端口流量上行峰值速率分布"
          );
          this.initChart(
            "chartWrapper4",
            this.in_avg,
            "PON端口流量上行均值速率分布"
          );
          console.log(this.out_peak);
          this.loading = false
        });
    }
  }
};
</script>

<style scoped>
</style>



