<template>
  <div>
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <div class="block">
      <el-date-picker v-model="time" type="month" placeholder="选择月"></el-date-picker>
      <el-button>提交</el-button>
    </div>
    <el-table :data="tableData" style="width: 100%" height="500">
      <el-table-column prop="time" label="日期" width="180" fixed></el-table-column>
      <el-table-column prop="infiltration_ratio" label="视频用户渗透率" width="180"></el-table-column>
      <el-table-column prop="demand_rate_ave" label="点播用户平均速率" width="180"></el-table-column>
      <el-table-column prop="demand_simultaneously" label="点播并发率" width="180"></el-table-column>
      <el-table-column prop="broadband_user_demand_rate_ave" label="宽带用户平均视频点播速率" width="180"></el-table-column>
      <el-table-column prop="live_user_rate_ave" label="直播用户平均速率" width="180"></el-table-column>
      <el-table-column prop="live_simultaneously" label="直播并发率" width="180"></el-table-column>
      <el-table-column prop="broadband_user_live_rate_ave" label="宽带用户平均视频直播速率" width="180"></el-table-column>
      <el-table-column prop="online_user_simultaneously" label="上网用户并发率" width="180"></el-table-column>
      <el-table-column prop="online_user_rate_ave" label="帧率" width="180"></el-table-column>
      <el-table-column prop="broadband_user_online_rate_ave" label="宽带用户平均上网速率" width="180"></el-table-column>
      <el-table-column prop="broadband_user_rate_ave" label="宽带用户平均速率" width="180"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      options: [
        {
          value: "北京",
          label: "北京"
        }
      ],
      value: "北京",
      time: "",
      sendMonth: "2017/6",
      tableData: []
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/calculateParametersWithIPTVAndBRAS/",
      params: {
            time: this.sendMonth
      }
    }).then(res => {
      if (res.data.success) {
        let temtableData = []
        let len = res.data.data.time.length
        for (let i=0;i<len;i++) {
          let a = {}
          temtableData.push(a)
        }
        let allData = res.data.data;
        for (let key in allData) {
          allData[key].forEach((item,index) => {
            console.log(item, index)
            temtableData[index][key] = item
            console.log(temtableData[index][key])
          })
        }
        console.log(temtableData)
        this.tableData = temtableData
      }
    });
  }
};
</script>

<style>
</style>



