<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true">新建</el-button>

    <el-dialog title="设置参数" :visible.sync="dialogFormVisible">
      <div>
        类型：
        <el-input v-model="newType" placeholder="请输入类型" size="medium" class="elInput"></el-input>分辨率：
        <el-input v-model="newResolving_power" placeholder="请输入分辨率" class="elInput"></el-input>
        <br />帧率：
        <el-input v-model="newFrame_rate" placeholder="请输入帧率" class="elInput"></el-input>颜色位数：
        <el-input v-model="newColor_bits" placeholder="请输入颜色位数" class="elInput"></el-input>
        <br />压缩技术：
        <el-input v-model="newCompression_technique" placeholder="请输入压缩技术" class="elInput"></el-input>
        <br />最小速率：
        <el-input v-model="newMinimum_rate" placeholder="请输入最小速率" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改参数" :visible.sync="dialogForm2Visible">
      <div>
        类型：
        <el-input v-model="newType" placeholder="请输入类型" size="medium" class="elInput"></el-input>分辨率：
        <el-input v-model="newResolving_power" placeholder="请输入分辨率" class="elInput"></el-input>
        <br />帧率：
        <el-input v-model="newFrame_rate" placeholder="请输入帧率" class="elInput"></el-input>颜色位数：
        <el-input v-model="newColor_bits" placeholder="请输入颜色位数" class="elInput"></el-input>
        <br />压缩技术：
        <el-input v-model="newCompression_technique" placeholder="请输入压缩技术" class="elInput"></el-input>
        <br />最小速率：
        <el-input v-model="newMinimum_rate" placeholder="请输入最小速率" class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false">取 消</el-button>
        <el-button type="primary" @click="updateNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%" height='550'>
      <el-table-column prop="type" label="类型" width="180" fixed></el-table-column>
      <el-table-column prop="Resolving_power" label="分辨率" width="180"></el-table-column>
      <el-table-column prop="Frame_rate" label="帧率" width="180"></el-table-column>
      <el-table-column prop="Color_bits" label="颜色位数" width="180"></el-table-column>
      <el-table-column prop="compression_technique" label="压缩技术" width="180"></el-table-column>
      <el-table-column prop="Minimum_rate" label="帧率" width="最小速率(MB)"></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small">删除</el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [
        {
          type: "\u6807\u6e05 SD",
          Frame_rate: "25/30",
          Color_bits: 8,
          compression_technique: "H.264",
          Minimum_rate: 2,
          Resolving_power: "640*480",
          id: 1
        }
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newType: "",
      newFrame_rate: "",
      newColor_bits: "",
      newCompression_technique: "",
      newMinimum_rate: "",
      newResolving_power: "",
      operationId: '',
      formLabelWidth: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFromBusiness_parameters/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      axios({
        method: "get",
        url: "/api/insertIntoBusiness_parameters/",
        params: {
          type: this.newType,
          Frame_rate: this.newFrame_rate,
          Color_bits: this.newColor_bits,
          compression_technique: this.newCompression_technique,
          Minimum_rate: this.newMinimum_rate,
          Resolving_power: this.newResolving_power
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data
          alert("新建成功");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id
      this.newType = e.type;
      this.newFrame_rate = e.Frame_rate;
      this.newColor_bits = e.Color_bits;
      this.newCompression_technique = e.compression_technique;
      this.newMinimum_rate = e.Minimum_rate;
      this.newResolving_power = e.Resolving_power;
      this.dialogForm2Visible = true;
    },
    updateNew() {
      axios({
        method: "get",
        url: "/api/updateFromBusiness_parameters/",
        params: {
          id: this.operationId,
          type: this.newType,
          Frame_rate: this.newFrame_rate,
          Color_bits: this.newColor_bits,
          compression_technique: this.newCompression_technique,
          Minimum_rate: this.newMinimum_rate,
          Resolving_power: this.newResolving_power
        }
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert('修改成功')
          this.dialogForm2Visible = false;
        } else {
          alert('修改失败')
          this.dialogForm2Visible = false;
        }
      })
    },
    del_row(e) {
      if (confirm("确认要删除吗？")) {
        axios({
          method: "get",
          url: "/api/deleteFromBusiness_parameters/",
          params: {
            id: e.id
          }
        }).then(res => {
          if (res.data.success) {
            this.tableData = res.data.data;
            alert("删除成功");
          } else {
            alert("删除失败");
          }
        });
      }
    }
  }
};
``;
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




