<template>
  <div>
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <div class="block">
      <span class="demonstration">月</span>
      <el-date-picker v-model="time" type="month" placeholder="选择月"></el-date-picker>
      <el-button @click="btn">提交</el-button>
    </div>
    <div id="IpBasc" style="width: 1000px;height: 500px;background: #F0B27A" class="chart"></div>
    <div id="BandUserCount" style="width: 1000px;height: 500px;background: #ABEBC6" class="chart"></div>
    <div id="BandAllUserCount" style="width: 1000px;height: 500px;background: #F0B27A" class="chart"></div>
  </div>
</template>

<script>
import echarts from "echarts";
import axios from "axios";
export default {
  data() {
    return {
      options: [
        {
          value: "北京",
          label: "北京"
        }
      ],
      value: "北京",
      time: "",
      sendMonth: "2019/10",
      IpBascData: {},
      BandUserCountData: {}
    };
  },
  methods: {
    initIpBasc(data) {
      let myChart = echarts.init(document.getElementById("IpBasc"));
      // console.log(data.date, data.basc, data.iptv);
      let option = {
        title: {
          text: "IPTV宽带用户发展情况"
        },
        legend: {
          data: ["普通宽带用户数", "IPTV宽带用户数"],
          align: "left",
          left: 300
        },

        toolbox: {
          feature: {
            magicType: {
              type: ["stack", "tiled"]
            },
            dataView: {}
          }
        },
        tooltip: {},
        xAxis: {
          data: data.returnTime, // []
          name: "X Axis",
          silent: false,
          axisLine: { onZero: true },
          splitLine: { show: false },
          splitArea: { show: false }
        },
        yAxis: {
          splitArea: { show: false }
        },
        series: [
          {
            name: "普通宽带用户数",
            type: "bar",
            stack: "one",
            data: data.returnBRAS
          },
          {
            name: "IPTV宽带用户数",
            type: "bar",
            stack: "one",
            data: data.returnIPTV
          }
        ]
      };
      myChart.setOption(option);
    },
    initBandUserCount(data) {
      let myChart = echarts.init(document.getElementById("BandUserCount"));
      let option = {
        title: {
          text: "宽带用户数"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["100M", "200M", "300M", "500M", "600M"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: data.returnTime
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "100M",
            type: "line",
            data: data.return100
          },
          {
            name: "200M",
            type: "line",
            data: data.return200
          },
          {
            name: "300M",
            type: "line",
            data: data.return300
          },
          {
            name: "500M",
            type: "line",
            data: data.return500
          },
          {
            name: "600M",
            type: "line",
            data: data.return600
          }
        ]
      };
      myChart.setOption(option);
    },
    initBandAllUserCount(data) {
      let myChart = echarts.init(document.getElementById("BandAllUserCount"));
      let option = {
        title: {
          text: "宽带活跃总数"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["宽带活跃总数"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: data.returnTime
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "宽带活跃总数",
            type: "line",
            stack: "总量",
            data: data.total
          }
        ]
      };
      myChart.setOption(option);
    },
    btn() {
      let d = new Date(this.time);
      this.sendMonth = d.getFullYear() + "/" + (d.getMonth() + 1);
      this.getIpBascData().then(() => this.initIpBasc(this.IpBascData));
      this.getBandUserCountData().then(() => {
        this.initBandUserCount(this.BandUserCountData)
        this.initBandAllUserCount(this.BandUserCountData)
      })
    },
    getIpBascData() {
      return new Promise((resolve, reject) => {
        axios({
          method: "get",
          url: "/api/AllOfIPTVandBRASRegisterUser/",
          params: {
            time: this.sendMonth
          }
        }).then(res => {
          if (res.data.success) {
            this.IpBascData = res.data.data;
            console.log(res.data.data);
            resolve();
          } else {
            reject("error");
          }
        });
      });
    },
    getBandUserCountData() {
      return new Promise((resolve, reject) => {
        axios({
          method: "get",
          url: "/api/AllFromBRASUserNum/",
          params: {
            time: this.sendMonth
          }
        }).then(res => {
          if (res.data.success) {
            this.BandUserCountData = res.data.data;
            console.log(this.BandUserCountData);
            resolve();
          } else {
            reject("error");
          }
        });
      });
    }
  },
  mounted() {
    this.getIpBascData().then(() => this.initIpBasc(this.IpBascData));
    this.getBandUserCountData().then(() => {
      this.initBandUserCount(this.BandUserCountData)
      this.initBandAllUserCount(this.BandUserCountData)
    })
  }
};
</script>

<style>
.chart {
  margin: 20px;
}
</style>



