<template>
<div class='bdmap'>
        <div id="chart_map"></div>
        <div id="pointInfo"></div>
</div>
</template>

<script>
import "../GeoUtils.js"
import echarts from 'echarts'
// import Bus from '@/assets/Bus.js'
import axios from 'axios'
export default {
  data () {
    return {
        menulist:[]
    }
  },
  methods:{
    initMap() {
      var points = [
            {"lng":116.36434,"lat":39.965187,"id":1,"name":"10.24.113.206",},
            {"lng":116.372964,"lat":39.964745,"id":2,"name":"119.164.223.218"},
            {"lng":116.351692,"lat":39.958551,"id":3,"name":"60.208.68.78"},
            {"lng":116.355392,"lat":39.987588,"id":4,"name":"北京航空航天大学"},
            {"lng":116.323197,"lat":39.976088,"id":5,"name":"中国人民大学"},
            {"lng":116.329952,"lat":40.009586,"id":6,"name":"清华大学"},
        ];
        var markers = [];
        var pointCount = 0;
        var nameInside = [];

        function addMarker(points) {
            var pointCount = 0;
            for(var i=0, pointsLen = points.length; i<pointsLen; i++) {
                var point = new BMap.Point(points[i].lng,points[i].lat);
                var marker = new BMap.Marker(point);
                map.addOverlay(marker);
                points.push(point);

                (function() {
                    var thePoint = points[i];

                    if(BMapLib.GeoUtils.isPointInCircle(point,circle)){
                        pointCount++;
                        nameInside.push(points[i].name)
                    }
                    marker.addEventListener("click",
                        function() {
                            showInfo(this,thePoint);
                        });
                })();
            }
        }
        function showInfo(thisMarker,point) {
            var OLT_ON_CLICK = point.name;
            var opts = {
                width : 500,     // 信息窗口宽度
                height: 400,     // 信息窗口高度
            }
            var sContent =
                "<h4 style='margin:0 0 5px 0;padding:0.2em 0;text-align: center'>"+point.name+"</h4>"
                +"<div id='OLT_Chart' style='margin-top: 10px;width:500px;height:250px;'></div>"
                +"<div id='OLT_INFO' style='margin-top: 10px;width:500px;height:130px;'></div>"
            var infoWindow = new BMap.InfoWindow(sContent,opts); //创建信息窗口对象
            thisMarker.openInfoWindow(infoWindow); //图片加载完后重绘infoWindow
            setTimeout(fun,500);
        }
        var styleOptions = {
            strokeColor:"red",    //边线颜色。
            fillColor:"red",      //填充颜色。当参数为空时，圆形将没有填充效果。
            strokeWeight: 3,       //边线的宽度，以像素为单位。
            strokeOpacity: 0.8,	   //边线透明度，取值范围0 - 1。
            fillOpacity: 0.6,      //填充的透明度，取值范围0 - 1。
            strokeStyle: 'solid' //边线的样式，solid或dashed。
        }
        var map = new BMap.Map("chart_map");
        var circle = new BMap.Circle(new BMap.Point(116.36434, 39.965187),1000,{fillColor:"blue", strokeWeight: 1 ,fillOpacity: 0.3, strokeOpacity: 0.3});
        map.centerAndZoom(new BMap.Point(116.303197, 39.996088), 14);  // 设置中心点和缩放级别
        map.setCurrentCity("北京");
        map.addControl(new BMap.MapTypeControl());
        map.enableScrollWheelZoom(true);
        function ZoomControl(){
            // 默认停靠位置和偏移量
            this.defaultAnchor = BMAP_ANCHOR_TOP_LEFT;
            this.defaultOffset = new BMap.Size(5, 50); // 距离左上角位置
        }
        // 通过JavaScript的prototype属性继承于BMap.Control
        ZoomControl.prototype = new BMap.Control();

        // 自定义控件必须实现自己的initialize方法,并且将控件的DOM元素返回
        // 在本方法中创建个div元素作为控件的容器,并将其添加到地图容器中
        ZoomControl.prototype.initialize=function (map) {
            // 创建一个DOM元素
            var div = document.createElement("div");
            // 添加文字说明
            div.appendChild(document.createTextNode("清除选中"));
            // 设置样式
            div.style.cursor = "pointer";
            div.style.border = "1px solid gray";
            div.style.height="30px";
            div.style.width="65px";
            div.style.color="#336699";
            div.style.backgroundColor = "white";
            div.style.borderRadius="4px";
            div.style.lineHeight="30px";
            div.style.textAlign="center";
            // 绑定事件
            div.onclick =  function clearAll(e) {
                for(var i = 0; i < overlays.length; i++){
                    map.removeOverlay(overlays[i]);
                }
                overlays.length = 0
            }
            // 添加DOM元素到地图中
            map.getContainer().appendChild(div);
            // 将DOM元素返回
            return div;
        }
        // 创建控件
        var myZoomCtrl = new ZoomControl();
        // 添加到地图当中
        map.addControl(myZoomCtrl);



        addMarker(points);
        map.addOverlay(circle);

        circle.addEventListener("click",clickCircle)

        function clickCircle() {
            alert("分别是："+nameInside.join(","))

        };
        var drawingManager = new BMapLib.DrawingManager(map, {
            isOpen: false, //是否开启绘制模式
            enableDrawingTool: true, //是否显示工具栏
            drawingToolOptions: {
                anchor: BMAP_ANCHOR_TOP_LEFT, //位置
                offset: new BMap.Size(5, 5), //偏离值
                scale: 0.8 //工具栏缩放比例
            },
            circleOptions: styleOptions, //圆的样式
            polylineOptions: styleOptions, //线的样式
            polygonOptions: styleOptions, //多边形的样式
            rectangleOptions: styleOptions //矩形的样式
        });
        var overlays = [];

        var overlaycomplete = function(e){
            var ArrayName=[];
            var pointCount1 = 0;
            overlays.push(e.overlay);
            var overlaysLength=overlays.length;
            for(var i=0;i<points.length;i++){
                if(BMapLib.GeoUtils.isPointInPolygon(points[i],overlays[overlaysLength-1])){
                    pointCount1++;
                    ArrayName.push(points[i-6].name);
                }
            }
            console.log(pointCount1);
            alert("区域中有"+pointCount1+"个点"+"分别是："+ArrayName.join(","));
            points.splice(12);
            drawingManager.close();
        };
        drawingManager.addEventListener('overlaycomplete', overlaycomplete);
        function fun(){
            var myChart1 = echarts.init(document.getElementById('OLT_Chart'));
            var data1 = {
                "name": "机房1",
                "children": [
                    {
                        "name": "OLT1",
                        "children": [
                            {"name": "PON1", "value": 8833},
                            {"name": "PON2", "value": 1732},
                            {"name": "PON3", "value": 3623},
                            {"name": "PON4", "value": 8833},
                            {"name": "PON5", "value": 1732},
                            {"name": "PON6", "value": 3623},
                            {"name": "PON7", "value": 8833},
                            {"name": "PON8", "value": 1732}
                        ]
                    },
                    {
                        "name": "OLT2",
                        "children": [
                            {"name": "PON1", "value": 8833},
                            {"name": "PON2", "value": 1732},
                            {"name": "PON3", "value": 3623},
                            {"name": "PON4", "value": 8833},
                            {"name": "PON5", "value": 1732},
                            {"name": "PON6", "value": 3623},
                            {"name": "PON7", "value": 8833},
                            {"name": "PON8", "value": 1732}
                        ]
                    }
                ]
            };
            var option = {
                tooltip: {
                    trigger: 'item',
                    triggerOn: 'mousemove'
                },
                legend: {
                    top: '2%',
                    left: '3%',
                    orient: 'vertical',
                    data: [{
                        name: '机房',
                        icon: 'rectangle'
                        }],
                    borderColor: '#c23531'
                },
                series:[
                    {
                        type: 'tree',

                        name: '机房',

                        data: [data1],

                        symbolSize: 10,

                        label: {
                                position: 'bottom',
                                distance: 5,
                                verticalAlign: 'middle',
                                align: 'center'
                        },

                        leaves: {
                            label: {
                                    position: 'right',
                                    distance: 20,
                                    verticalAlign: 'middle',
                                    align: 'middle'
                            }
                        },

                        expandAndCollapse:false,

                        animationDuration: 550,
                        animationDurationUpdate: 750

                    }
                ]
            };
            myChart1.setOption(option);

            myChart1.on('click',function (params){
                console.log("单击项",params.name);

                addtable(params.name);
                //myChart1.setOption(option);

            });
            function addtable (name){
                var table = document.createElement("table");
                table.style.border="1px";
                document.getElementById('OLT_INFO').innerHTML = "";
                document.getElementById('OLT_INFO').appendChild(table);
                //创建表头
                var thead = document.createElement("thead");
                //将标题追加到table中
                table.appendChild(thead);
                //创建tr
                var tr =document.createElement("tr");
                //将tr追加到thead中
                thead.appendChild(tr);
                //设置tr的样式属性
                tr.style.height="50px";
                tr.style.backgroundColor = "lightgray";
                for(var i =0;i<5;i++) {
                    var th = document.createElement("th");
                    //将th追加到thead中的tr中
                    th.innerHTML=name;
                    th.style.width="100px";
                    tr.appendChild(th);
                }
            }
        } 
    },
    hide(e,obj){
        e.cancelBubble=true
        let tar =e.currentTarget.parentElement.parentElement.children;
        if (obj.hide==true){
            obj.hide=false;
            if (tar.length>0){
            for (let i=1;i<tar.length;i++){
                    tar[i].style.display='block'
                }
            }
        }
        else{
            obj.hide = true;
            if (obj.level!=3 && tar.length>1){
                for (let i=1;i<tar.length;i++){
                    tar[i].style.display='none'
                }
            }
        }   
    },
    showInfo(obj){
        Bus.$emit("getItem",obj)
    },

  },
  created(){
    
  },
  mounted(){
    this.initMap();
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

@media screen and (max-width:960px){
.bdmap{
    height:auto;
    width: 100%;
    position: relative;
    margin-top:20px;
    margin-left:20px;
    margin-right:20px;
    display: flex ;
    flex-direction: column;
    
}
    #pointInfo{
    margin-top:5px;
    height: 400px;
    width: calc(100% - 42px);
    position: relative;
    background-color: #B4C7E4;
    
    }
    #chart_map{
    height: 600px;
    width: calc(100% - 42px);
    position: relative;
    border: 1px solid #2C58A6;
    }
    
}
@media screen and (min-width:961px){
.bdmap{
    height:calc(100% - 115px);
    width: calc(100% - 42px);
    position: relative;
    margin-top:20px;
    margin-left:20px;
    margin-right:20px;
    display: flex;
    
}
#pointInfo{
    height: 100%;
    width: 25%;
    position: relative;
    background-color: #B4C7E4;
}
#chart_map{
    height: 100%;
    width: 75%;
    position: relative;
        border: 1px solid #2C58A6;
}
}
</style>


