<template>
  <div>
    <h1>OLT PON 口南向设备间连接关系</h1>
    <div class="chartWrapper">
      <div id="view_pon_user_number" class="pieChart1"></div>
      <div id="view_olt_up_road" class="pieChart1"></div>
      <div id="view_slot_occupy_rate" class="pieChart1"></div>
      <div id="view_pon_used_rate" class="pieChart1"></div>
    </div>
    <el-table :data="tableData" style="width: 100%" height="550">
      <el-table-column
        v-for="(columnData,index) in tableInitData"
        :key="index"
        :prop="columnData[0]"
        :label="columnData[1]"
        width="180"
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
import echarts from "echarts";
export default {
  data() {
    return {
      tableInitData: [
        ["id", "id"],
        ["OLT_IP", "OLT_IP"],
        ["slot_occupy_rate", "槽位占用率"],
        ["up_port_occupy_rate", "上联口占用率"],
        ["PON_port_occupy_rate", "PON口占用率"],
        ["user_number", "用户编号"]
      ],
      tableData: [
        {
          OLT_IP: "10.45.36.10",
          up_port_occupy_rate: 0.7142857142857143,
          slot_occupy_rate: 0.6666666666666666,
          PON_port_occupy_rate: 0.6666666666666666,
          user_number: 0,
          id: "1"
        }
      ],
      chartData: {}
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/PON_device_power/"
    }).then(res => {
      if (res.data.success) {
        console.log(res.data);
        this.tableData = res.data.data.table_data;
        this.chartData = res.data.data.view_data;
        console.log(this.chartData);
        let view_slot_occupy_rate_data = this.initData(
          this.chartData.view_slot_occupy_rate
        );
        let view_pon_user_number_data = this.initData(
          this.chartData.view_pon_user_number
        );
        let view_olt_up_road_data = this.initData(
          this.chartData.view_olt_up_road
        );
        let view_pon_used_rate_data = this.initData(
          this.chartData.view_pon_used_rate
        );
        console.log(view_slot_occupy_rate_data);
        this.initPieChart('view_pon_user_number',view_pon_user_number_data,'OLT下挂用户数')
        this.initPieChart('view_olt_up_road',view_olt_up_road_data,'OLT上行链路')
        this.initPieChart("view_slot_occupy_rate", view_slot_occupy_rate_data,'OLT槽位利用率');
        this.initPieChart('view_pon_used_rate',view_pon_used_rate_data, '已配置PON口利用率')
      }
    });
  },
  methods: {
    initData(data) {
      let result = [];
      for (let item in data) {
        let obj = {};
        obj.value = data[item];
        obj.name = item;
        result.push(obj);
      }
      return result;
    },
    initPieChart(domName, data, title) {
      let myChart = echarts.init(document.getElementById(domName));
      let legendData = []
      data.map((item) => {
        legendData.push(item.name)
      })
      let option = {
        title: {
          text: name,
          subtext: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
            orient: 'vertical',
            left: 'right',
            data: legendData
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: data,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>

<style lang="stylus" scoped>
.chartWrapper {
  display: flex;
  // background: #CCCC33;
  justify-content: space-between;

  .pieChart1 {
    width: 300px;
    height: 250px;
    background: #D6DBDF;
  }
}
</style>



