<template>
    <el-menu
      :default-active="$route.path"
      router
      class="el-menu-vertical-demo"
      :default-openeds='opens'
      background-color="#fff"
      text-color="#333"
      style="margin-top:20px"
      >
      <el-submenu :index="sidebardata.path">
        <template slot="title">
          <span>{{sidebardata.name}}</span>
        </template>
          <el-submenu v-for='item in sidebardata.children' :key="item.name" :index="item.path">
            <template slot="title">
              <!-- <i class="el-icon-menu"></i> -->
              <span>{{item.name}}</span>
            </template>
            <el-menu-item style="height:40px;line-height: 40px; padding-left:40px; color: grey" v-for="option in item.children" :index='option.path' :key="option.path">{{option.name}}</el-menu-item>
          </el-submenu>
      </el-submenu>
    </el-menu>
</template>

<script>
export default {
  // data(){
  //   return {
  //     opens:[]
  //   }
  // },
     methods: {
      handleOpen(key, keyPath) {
      },
      handleClose(key, keyPath) {
      }
    },
    props:[
      "data","open"
    ],
    computed:{
      sidebardata(){
        return this.data
      },
      opens(){
        return this.open
      }
    }

}
</script>

<style>
.el-menu{
    border-right: solid 1px white !important;
}
.el-submenu .el-menu-item {
    padding: 0 0 0 0;
    min-width: 200px;
}
 .el-menu-item.is-active {
    color: #6b98e6 !important;
    border-right: solid 4px #6b98e6
}
.el-menu-item{
  padding:0 0 0 0;

  /* text-align: right; */
}

</style>



