<template>
  <div>
    <h1>异常数据</h1>
    <router-link to="/networkResources/userNeed/PonFlowTrend"><el-button type="primary">返回</el-button></router-link>
    <div class="wrapper">
      <div
        v-for="(n,index) in diffCount"
        :key="n"
        :id="setDiffId(index)"
        class="diffChart"
      >
      </div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      diffData: [],
      diffCount: 0,
      diffChartData: {}
    }
  },
  mounted() {
    this.diffData = this.$store.getters.getDiffData
    this.diffCount = this.diffData.length
    this.dataPreProcess(this.diffData,'diff',this.diffChartData)
    setTimeout(()=> {
      let ele = document.querySelectorAll('.diffChart')
      Array.prototype.forEach.call(ele, (item, index)=>{
        let data
        let key = 'diff' + index
        data = this.diffChartData[key]
        this.initChart(data.name, data.xData, data.avgData, data.peakData, data.trendLineData,data.title)
      })
    })
  },
  methods: {
    setDiffId(index) {
      return 'diff' + index
    },
    dataPreProcess(data,datatype,dataDes) {
      data.map((item, index) => {
      let name = `${datatype}${index}`
      dataDes[name] = {}
      let that = dataDes[name]
      that.name = name
      that.xData = item.samples.x
      that.avgData = item.samples.avg
      that.peakData = item.samples.peak
      let tl = item.trend_line
      that.tl = item.trend_line
      that.trendLineData = [[tl.x1,tl.y1],[tl.x2,tl.y2]]
      that.title = `${item.olt}--${item.pon_board}--${item.pon_port}`
      })
    },
    initChart(domName, xData, avgData, peakData, trendLineData, title) {
      let myChart = echarts.init(document.getElementById(domName));
      let option = {
        title: {
          text: title,
          textStyle: {
            fontSize: 15
          }
        },
        tooltip: {
          show: true,
          position: 'right'
        },
        color: ["#0099FF", "#99CC00",'#F00000'],
        backgroundColor: '#fff',
        xAxis: {
          data: xData,
          axisLabel: {
            interval: 8,
            rotate: 45,
            fontSize: 8,
          },
        },
        yAxis: {},
        grid: {
          // top: 10,
          bottom: 45,
          left: 30,
        },
        series: [
          {
            symbolSize: 5,
            type: "scatter",
            data: avgData,
            label: {
              // show: true
            }
          },
          {
            symbolSize: 5,
            data: peakData,
            type: "scatter",
          },
          {
            symbolSize: 10,
            data: trendLineData,
            type: "line",
          }
        ]
      };

      myChart.setOption(option);
      // window.addEventListener("resize", function() {
      //   myChart.resize();
      // });
    },
  },
}
</script>

<style scope>
  .wrapper {
    display: flex;
    flex-wrap: wrap
  }
  .diffChart {
    width: 300px;
    height: 300px
  }
</style>



