<template>
  <div>
    <div class="container">
      <div class="loginWrapper">
        <h1>login</h1>
        <el-input placeholder="请输入用户名" v-model="login_name" clearable></el-input>
        <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
      </div>
      <el-button type="primary" @click="loginSubmit">登录</el-button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      login_name: "",
      password: ""
    };
  },
  methods: {
    loginSubmit() {
      axios({
        method: "get",
        url: "/auth/login/",
        params: {
          login_name: this.login_name,
          password: this.password
        }
      }).then((res) => {
        if(res.data.success) {
          this.$store.dispatch("getNewIsLogin", true)
          this.$store.dispatch("getNewUserName", this.login_name)
          alert('登陆成功')
          this.$router.push('/')
        }else {
          alert('用户名或密码错误')
        }
      })
    }
  }
};
</script>

<style>
.loginWrapper {
  width: 300px;
  height: 300px;
  display: inline-block;
  flex-direction: column;
  align-content: space-between;
}
.container {
  text-align: center;
}
</style>



