<template>
  <div>
    <h1>网络能力链路树</h1>
    <el-table :data="tableData" style="width: 100%" height="550">
        <el-table-column
          v-for="(columnData,index) in tableInitData"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableInitData: [
        ["Broadband_account", "宽带号"],
        ["bandwidth", "带宽"],
        ["IPTV_account", "IPTV号"],
        ["MDUIP", "MDUIP"],
        ["MDU_port", "MDU号"],
        ["OS_id", "OS_ID"],
        ["olt_id", "olt_id"],
        ["olt_down_port", "olt下联板号"],
        ["choosed_olt_up_port", "olt上联板号"],
        ["LSW_IP", "LSW_IP"],
        ["LSW_down_port", "LSW下联板号"],
        ["choosed_lsw_up_port", "LSW上联板号"],
        ["BRAS_IP", "BRAS_IP"],
        ["BRAS_port", "BRAS_port"],
        ["other_equip_IP", "其他设备IP"],
        ["equip_port", "设备号"],
      ],
      tableData: [
        {
          onu_manufacture: "HW",
          onu_type: "MDU",
          onu_ip: "10.41.24.10",
          OS_id: "163943309",
          olt_id: "10.45.36.17",
          onu_serial_number: "BJ00000000",
          onu_id: "16",
          olt_port: "0/0/17/5"
        }
      ]
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/link_tree/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



