<template>
  <div>
    <h1>高流量用户都用什么套餐</h1>
    <selectTwo :search="search"></selectTwo>
    <div class="sumChart" id="sumChart"></div>
    <div class="chartWrapper" v-loading="loading">
      <div class="barChartWrapper">
        <div id="barChart10" class="barChart"></div>
        <div id="barChart100" class="barChart"></div>
        <div id="barChart1000" class="barChart"></div>
      </div>
      <div class="Divide" v-show="diviedeVisible"></div>
      <div class="pieChartWrapper">
        <div id="pieChart10" class="barChart"></div>
        <div id="pieChart100" class="barChart"></div>
        <div id="pieChart1000" class="barChart"></div>
      </div>
    </div>
  </div>
</template>

<script>
import selectTwo from "@/components/basic/selectTwo";
import echarts from "echarts";
import axios from "axios";
export default {
  data() {
    return {
      barUpData: {
        k_1000_data: {},
        k_100_data: {},
        k_10_data: {}
      },
      barDownData: {
        k_1000_data: {},
        k_100_data: {},
        k_10_data: {}
      },
      sumData: {
        '100M': [],
        '200M': [],
        '300M': [],
        '500M': []
      },
      diviedeVisible: false,
      loading: false
    };
  },
  components: {
    selectTwo
  },
  methods: {
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let sta = this.$store.getters.getStation;
      this.sumData = {
        '100M': [],
        '200M': [],
        '300M': [],
        '500M': []
      },
      Promise.all([
        axios({
          method: "get",
          url: "/api/aaa/head/user-list/",
          params: {
            department: dep,
            station: sta,
            k: 1000,
            date: 20190102
          }
        }),
        axios({
          method: "get",
          url: "/api/aaa/head/user-list/",
          params: {
            department: dep,
            station: sta,
            k: 100,
            date: 20190102
          }
        }),
        axios({
          method: "get",
          url: "/api/aaa/head/user-list/",
          params: {
            department: dep,
            station: sta,
            k: 10,
            date: 20190102
          }
        })
      ])
        .then(result => {
          let k_1000_data = result[0].data.data;
          let k_100_data = result[1].data.data;
          let k_10_data = result[2].data.data;
          this.barUpData.k_1000_data = k_1000_data.upstream_count;
          this.barDownData.k_1000_data = k_1000_data.downstream_count;

          this.barUpData.k_100_data = k_100_data.upstream_count;
          this.barDownData.k_100_data = k_100_data.downstream_count;

          this.barUpData.k_10_data = k_10_data.upstream_count;
          this.barDownData.k_10_data = k_10_data.downstream_count;

          let k_1000 = this.filterData(this.barUpData.k_1000_data);
          for(let key in k_1000) {
            this.sumData[key].unshift(k_1000[key])
          }
          let k_100 = this.filterData(this.barUpData.k_100_data);
          for(let key in k_100) {
            this.sumData[key].unshift(k_100[key])
          }

          let k_10 = this.filterData(this.barUpData.k_10_data);
          for(let key in k_10) {
            this.sumData[key].unshift(k_10[key])
          }
          let a = []
          let b = []
          for(let c in this.sumData) {
            //
            let d = Array.from(this.sumData[c])
            let e = Array.from(this.sumData[c])
            // console.log(d)
            a.push(d)
            // console.log(a)
            b.push(e)
          }
          for(let i=0;i<a.length;i++) {
            for(let j=0;j<a[0].length;j++) {
              let sum=0
              for(let k=0;k<a.length;k++) {
                // console.log(a[k][i])
                sum += a[k][j]
              }
              console.log(sum)
              b[i][j] = (a[i][j] / sum).toFixed(2)
            }
          }
          this.sumData['100M'] = b[0]
          this.sumData['200M'] = b[1]
          this.sumData['300M'] = b[2]
          this.sumData['500M'] = b[3]
        })
        .then(() => {
          this.initBarChart(
            "barChart10",
            this.barUpData.k_10_data,
            "前10%流量用户套餐分布"
          );
          this.initBarChart(
            "barChart100",
            this.barUpData.k_100_data,
            "前1%流量用户套餐分布"
          );
          this.initBarChart(
            "barChart1000",
            this.barUpData.k_1000_data,
            "前0.1%流量用户套餐分布"
          );
          this.initPieChart(
            "pieChart10",
            this.barUpData.k_10_data,
            "前10%流量用户套餐分布"
          );
          this.initPieChart(
            "pieChart100",
            this.barUpData.k_100_data,
            "前1%流量用户套餐分布"
          );
          this.initPieChart(
            "pieChart1000",
            this.barUpData.k_1000_data,
            "前0.1%流量用户套餐分布"
          );
          this.initSumChart(this.sumData)
          this.loading = false;
          this.diviedeVisible = true;
        });
    },
    initSumChart(data) {
      let myChart = echarts.init(document.getElementById('sumChart'));
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["100M", "200M", "300M", "500M"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value"
        },
        yAxis: {
          type: "category",
          data: ["10%", "1%", "0.1%"]
        },
        series: [
          {
            name: "100M",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data: data['100M']
          },
          {
            name: "200M",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data:  data['200M']
          },
          {
            name: "300M",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data:  data['300M']
          },
          {
            name: "500M",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data:  data['500M']
          },
        ]
      };
      myChart.setOption(option);
    },
    initBarChart(domName, data, title) {
      let myChart = echarts.init(document.getElementById(domName));
      // let [ a,b,c,d ] = data
      let key = [];
      let value = [];
      let map = ["100M", "200M", "300M", "500M"];
      let filterData = {
        "100M": 0,
        "200M": 0,
        "300M": 0,
        "500M": 0
      };
      for (let item in data) {
        if (map.indexOf(item) < 0) {
          continue;
        }
        filterData[item] = data[item];
      }
      for (let newItem in filterData) {
        key.push(newItem);
        value.push(data[newItem]);
      }
      let len = key.length;
      let series = new Array(len).fill({ type: "bar" });
      let option = {
        title: {
          text: title,
          subtext: "",
          x: "center",
          top: 2
        },
        legend: { top: 20 },
        tooltip: {},
        dataset: {
          source: [["product", ...key], ["数量", ...value]]
        },
        xAxis: { type: "category" },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: series
      };
      myChart.setOption(option);
    },
    initPieChart(domName, data, title) {
      let myChart = echarts.init(document.getElementById(domName));
      let key = [];
      let value = [];
      let filterData = {
        "100M": 0,
        "200M": 0,
        "300M": 0,
        "500M": 0
      };
      let map = ["100M", "200M", "300M", "500M"];
      for (let item in data) {
        if (map.indexOf(item) < 0) {
          continue;
        }
        filterData[item] = data[item];
      }
      for (let newItem in filterData) {
        key.push(newItem);
        let a = {};
        a.value = data[newItem];
        a.name = newItem;
        value.push(a);
      }
      let option = {
        title: {
          text: title,
          subtext: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: [...key]
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: value,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    },
    filterData(data) {
      let map = ["100M", "200M", "300M", "500M"];
      let filterData = {
        "100M": 0,
        "200M": 0,
        "300M": 0,
        "500M": 0
      };
      for (let item in data) {
        if (map.indexOf(item) < 0) {
          continue;
        }
        filterData[item] = data[item];
      }
      return filterData
    }
  }
};
</script>

<style>
.barChart {
  height: 400px;
  width: 500px;
  /* background-color: rgb(42, 192, 180); */
}
.chartWrapper {
  display: flex;
  justify-content: space-around;
}
.Divide {
  height: 1200px;
  width: 5px;
  background-color: #eee;
}
.sumChart {
  width: 1000px;
  height: 500px;
}
</style>



