<template>
  <div>
    <baidu-map class="bm-view" ak="DZSjokGwq1OaHmR4iEhN4O0TXb0ImGBS" center="北京">
      <bm-marker
        v-for="item in pointData"
        :key="item.id"
        :position="{lng: item.lng, lat: item.lat}"
        :title="item.station_number"
        @click="lookStation"
        :dragging="true"
      >
        <!-- <bm-label content="我爱北京天安门" :labelStyle="{color: 'red', fontSize : '24px'}" :offset="{width: -35, height: 30}"/> -->
      </bm-marker>
      <!-- <span v-for="item in pointData" :key="item.id">
        <bm-marker :position="{lng: item.lng, lat: item.lat}" :dragging="true" @click="infowindowOpen">
        </bm-marker>
      </span>-->
    </baidu-map>
    <el-dialog
      title="收货地址"
      :visible.sync="dialogTableVisible"
      width="800px"
      style="text-align: center"
    >
      <div id="station" style="width: 700px;height: 500px;">
        <h1>station</h1>
        <div class="stationWrapper">
          <div class="oltWrapper">
            <h2>olt</h2>
            <div v-for="olt in stationData.olt" :key="olt.id" class="oltImg" @click="detailInfo">
              <el-tooltip class="item" effect="dark" :content="'ip: ' + olt.ip + '  name:  ' + olt.name" placement="top-start">
                <img src="@/assets/olt.png" alt="1" :id="olt.id" :name="olt.ip"/>
              </el-tooltip>
            </div>
          </div>
          <div class="ponWrapper">
            <h2>BRAS</h2>
            <div v-for="bras in stationData.brascrsr" :key="bras.id" class="brasImg">
              <el-tooltip class="item" effect="dark" :content="'ip: ' + bras.ip + '  name:  ' + bras.name" placement="top-start">
                <img src="@/assets/bras.png" alt="" />
              </el-tooltip>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="olt详细信息" :visible.sync="dialogTableVisible1">
      <el-table :data="oltDetailData">
        <el-table-column property="olt_ip" label="olt_ip" width="180"></el-table-column>
        <el-table-column property="slot_number" label="slot_number" width="200"></el-table-column>
        <el-table-column property="slot-type(C,T,P)" label="slot-type(C,T,P)"></el-table-column>
        <el-table-column property="board-model" label="board-model"></el-table-column>
        <el-table-column property="used_port_count" label="used_port_count"></el-table-column>
        <el-table-column property="port_count" label="port_count"></el-table-column>
        <el-table-column property="trunk_board_type_id" label="trunk_board_type_id"></el-table-column>
        <el-table-column property="control_board_type_id" label="control_board_type_id"></el-table-column>
        <el-table-column property="pon_board_type_id" label="pon_board_type_id"></el-table-column>
        <!-- <el-table-column property="slot-type(C,T,P)" label="slot-type(C,T,P)"></el-table-column> -->
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import BaiduMap from "vue-baidu-map/components/map/Map.vue";
import BmMarker from "vue-baidu-map/components/overlays/Marker.vue";
import BmLabel from "vue-baidu-map/components/overlays/Label.vue";
import axios from "axios";
export default {
  data() {
    return {
      pointData: [
        {
          shared_operator_id: "CM",
          station_number: "1001",
          is_shared: "Y",
          lng: 116.38,
          lat: 39.96,
          station_address:
            "\u5317\u4eac\u5e02-\u6d77\u6dc0\u533a-\u4e2d\u5173\u6751\u8857\u9053-\u897f\u571f\u57ce\u8def-\u897f\u571f\u57ce\u5317-\u5317\u90ae\u4e1c\u5bb6\u5c5e\u533a-1\u53f7\u697c-1\u5355\u5143-1\u5ba4",
          station_name: "\u5317\u90ae\u4e1c\u673a\u623f",
          id: 1,
          unit: 1101020101
        }
      ],
      gridData: [],
      dialogTableVisible: false,
      dialogTableVisible1: false,
      nowStationNum: "",
      stationData: {
        olt: [
          {
            ip: "10.45.36.13",
            type: "OLT",
            id: 4,
            name: "BJHD-ZGC-BYD-OLT1"
          }
        ],
        brascrsr: [
          {
            ip: "10.45.192.15",
            type: "BRAS",
            id: 6,
            name: "BJ2J-ZGC-BSX-BRAS1"
          },
          {
            ip: "10.45.192.21",
            type: "BRAS",
            id: 12,
            name: "BJ2J-ZGC-BSX-BRAS2"
          },
          {
            ip: "10.45.192.27",
            type: "CR",
            id: 18,
            name: "BJ2J-ZGC-BSX-BRAS3"
          },
          {
            ip: "10.45.192.33",
            type: "CR",
            id: 24,
            name: "BJ2J-ZGC-BSX-BRAS4"
          },
          {
            ip: "10.45.192.39",
            type: "CR",
            id: 30,
            name: "BJ2J-ZGC-BSX-BRAS5"
          },
          {
            ip: "10.45.192.45",
            type: "SR",
            id: 36,
            name: "BJ2J-ZGC-BSX-BRAS6"
          },
          {
            ip: "10.45.192.51",
            type: "SR",
            id: 42,
            name: "BJ2J-ZGC-BSX-BRAS7"
          },
          {
            ip: "10.45.192.57",
            type: "SR",
            id: 48,
            name: "BJ2J-ZGC-BSX-BRAS8"
          }
        ]
      },
      oltDetailData: {}
    };
  },
  components: {
    BaiduMap,
    BmMarker,
    BmLabel
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/all_station/"
    }).then(res => {
      if (res.data.success) {
        this.pointData = res.data.data;
      }
    });
  },
  methods: {
    lookStation(e) {
      let nowStationNum = e.target.z.title;
      this.nowStationNum = nowStationNum;
      this.dialogTableVisible = true;
      axios({
        method: "get",
        url: "/api/alldevicesByIdLevelOne/",
        params: {
          station: nowStationNum
        }
      }).then(res => {
        if (res.data.success) {
          this.stationData = res.data.data[0]
          this.dialogTableVisible = true;
        }
      });
    },
    detailInfo(e) {
      this.dialogTableVisible1 = true;
      console.log(e.target.id,e.target.name)
      axios({
        method: "get",
        url: "/api/showInformationOfDevice/",
        params: {
          id: e.target.id,
          ip: e.target.name
        }
      })
      .then(res => {
        if(res.data.success) {
          this.oltDetailData = res.data.data
        }
      })
    }
  }
};
</script>

<style>
.bm-view {
  width: 100%;
  height: 800px;
}
.stationWrapper {
  display: flex;
  justify-content: space-around;
}
.oltWrapper {
  width: 300px;
  height: 400px;
  background-color: #f7dc6f;
}
.ponWrapper {
  width: 300px;
  height: 400px;
  background-color: #76d7c4;
}
.brasImg {
  display: inline-block;
  margin: 10px;
}
</style>



