<template>
  <div>
    <el-button type="text" @click="dialogFormVisible = true">新建</el-button>

    <el-dialog title="设置参数" :visible.sync="dialogFormVisible">
      <div v-for="(item,index) in forData" :key="index">
        {{item[1]}}
        <el-input v-model='item[3]' :placeholder='item[1]' class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改参数" :visible.sync="dialogForm2Visible">
      <div v-for="(item,index) in forData" :key="index">
        {{item[1]}}
        <el-input v-model='item[3]' :placeholder='item[1]' class="elInput"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm2Visible = false">取 消</el-button>
        <el-button type="primary" @click="updateNew">确 定</el-button>
      </div>
    </el-dialog>

    <el-table :data="tableData" style="width: 100%" height="550">
      <el-table-column prop="Set_meal_type" label="套餐类型" width="180" fixed></el-table-column>
      <el-table-column prop="Broadband_commitment_uplink" label="宽带承诺上行" width="180"></el-table-column>
      <el-table-column prop="Broadband_commitment_downlink" label="宽带承诺下行" width="180"></el-table-column>
      <el-table-column prop="IPTV_commitment_uplink" label="IPTV承诺上行" width="180"></el-table-column>
      <el-table-column prop="IPTV_commitment_downlink" label="IPTV承诺下行" width="180"></el-table-column>

      <el-table-column prop="OLT_uplink_minimum_bandwidth" label="OLT上行最低带宽" width="180"></el-table-column>
      <el-table-column prop="OLT_uplink_maximum_bandwidth" label="OLT上行最高带宽" width="180"></el-table-column>
      <el-table-column prop="OLT_downlink_maximum_bandwidth" label="OLT下行最低带宽" width="180"></el-table-column>
      <el-table-column prop="OLT_downlink_minimum_bandwidth" label="OLT下行最高带宽" width="180"></el-table-column>

      <el-table-column prop="BRAS_uplink_minimum_bandwidth" label="BRAS上行最低带宽" width="180"></el-table-column>
      <el-table-column prop="BRAS_uplink_maximum_bandwidth" label="BRAS上行最高带宽" width="180"></el-table-column>
      <el-table-column prop="BRAS_downlink_minimum_bandwidth" label="BRAS下行最低带宽" width="180"></el-table-column>
      <el-table-column prop="BRAS_downlink_maximum_bandwidth" label="BRAS下行最高带宽" width="180"></el-table-column>

      <el-table-column prop="Broadband_VLAN" label="宽带VLAN" width="180"></el-table-column>
      <el-table-column prop="On_demand_VLAN" label="点播VLAN" width="180"></el-table-column>
      <el-table-column prop="Live_broadcast_VLAN" label="直播VLAN" width="180"></el-table-column>

      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="del_row(scope.row)" type="text" size="small">删除</el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      forData: [
        ['Set_meal_type', "套餐类型","newSet_meal_type",'1'],
        ['Broadband_commitment_uplink', "宽带承诺上行",'newBroadband_commitment_uplink','1'],
        ['Broadband_commitment_downlink', "宽带承诺下行",'newBroadband_commitment_downlink','1'],
        ['IPTV_commitment_uplink', "IPTV承诺上行",'newIPTV_commitment_uplink','1'],
        ['IPTV_commitment_downlink', "IPTV承诺下行",'newIPTV_commitment_downlink','1'],
        ['OLT_uplink_minimum_bandwidth', "OLT上行最低带宽",'newOLT_uplink_minimum_bandwidth','1'],
        ['OLT_uplink_maximum_bandwidth', "OLT下行最低带宽",'newOLT_uplink_maximum_bandwidth','1'],
        ['OLT_downlink_maximum_bandwidth', "OLT下行最低带宽",'newOLT_downlink_maximum_bandwidth','1'],
        ['OLT_downlink_minimum_bandwidth', "OLT下行最高带宽",'newOLT_downlink_minimum_bandwidth','1'],
        ['BRAS_uplink_minimum_bandwidth', "BRAS上行最低带宽",'newBRAS_uplink_minimum_bandwidth','1'],
        ['BRAS_uplink_maximum_bandwidth', "BRAS上行最高带宽",'newBRAS_uplink_maximum_bandwidth','1'],
        ['BRAS_downlink_minimum_bandwidth', "BRAS下行最低带宽",'newBRAS_downlink_minimum_bandwidth','1'],
        ['BRAS_downlink_maximum_bandwidth', "BRAS下行最高带宽",'newBRAS_downlink_maximum_bandwidth','1'],
        ['Broadband_VLAN', "宽带VLAN",'newBroadband_VLAN','1'],
        ['On_demand_VLAN', "点播VLAN",'newOn_demand_VLAN','1'],
        ['Live_broadcast_VLAN', "直播VLAN",'newLive_broadcast_VLAN','1']
      ],
      tableData: [
        {
          Set_meal_type: "100M\u5355\u5bbd\u5e26",
          BRAS_uplink_maximum_bandwidth: 800,
          OLT_downlink_minimum_bandwidth: 100000,
          OLT_uplink_maximum_bandwidth: 100,
          IPTV_commitment_downlink: 0,
          BRAS_downlink_minimum_bandwidth: 120,
          Live_broadcast_VLAN: 0,
          id: 1,
          BRAS_downlink_maximum_bandwidth: 8000,
          BRAS_uplink_minimum_bandwidth: 24,
          OLT_uplink_minimum_bandwidth: 16,
          On_demand_VLAN: 0,
          OLT_downlink_maximum_bandwidth: 100000,
          Broadband_commitment_uplink: 20,
          Broadband_commitment_downlink: 100,
          Broadband_VLAN: null,
          IPTV_commitment_uplink: 0
        },        {
          Set_meal_type: "100M\u5355\u5bbd\u5e26",
          BRAS_uplink_maximum_bandwidth: 800,
          OLT_downlink_minimum_bandwidth: 100000,
          OLT_uplink_maximum_bandwidth: 100,
          IPTV_commitment_downlink: 0,
          BRAS_downlink_minimum_bandwidth: 120,
          Live_broadcast_VLAN: 0,
          id: 1,
          BRAS_downlink_maximum_bandwidth: 8000,
          BRAS_uplink_minimum_bandwidth: 24,
          OLT_uplink_minimum_bandwidth: 16,
          On_demand_VLAN: 0,
          OLT_downlink_maximum_bandwidth: 100000,
          Broadband_commitment_uplink: 20,
          Broadband_commitment_downlink: 100,
          Broadband_VLAN: null,
          IPTV_commitment_uplink: 0
        },
      ],
      dialogFormVisible: false,
      dialogForm2Visible: false,
      newSet_meal_type: "100M\u5355\u5bbd\u5e26",
      newBRAS_uplink_maximum_bandwidth: 800,
      newOLT_downlink_minimum_bandwidth: 100000,
      newOLT_uplink_maximum_bandwidth: 100,
      newIPTV_commitment_downlink: 0,
      newBRAS_downlink_minimum_bandwidth: 120,
      newLive_broadcast_VLAN: 0,
      newBRAS_downlink_maximum_bandwidth: 8000,
      newBRAS_uplink_minimum_bandwidth: 24,
      newOLT_uplink_minimum_bandwidth: 16,
      newOn_demand_VLAN: 0,
      newOLT_downlink_maximum_bandwidth: 100000,
      newBroadband_commitment_uplink: 20,
      newBroadband_commitment_downlink: 100,
      newBroadband_VLAN: null,
      newIPTV_commitment_uplink: 0,
      operationId: ""
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/AllFromSet_Meal/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
  methods: {
    addNew() {
      let params = {}
      this.forData.map((item) => {
        let key = item[0]
        let value = item[3]
        params[key] = value
      })
      console.log(params)
      axios({
        method: "get",
        url: "/api/insertIntoSet_Meal/",
        params: params
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert("新建成功");
          this.dialogFormVisible = false;
        }
      });
    },
    handleClick(e) {
      this.operationId = e.id;
      console.log(this.forData)
      this.forData.map((item,index) => {
        let key = item[0]
        item[3] = e[key]
      })
      this.dialogForm2Visible = true
    },
    updateNew() {
      let params = {}
      this.forData.map((item) => {
        let key = item[0]
        let value = item[3]
        params[key] = value
      })
      params.id = this.operationId
      axios({
        method: "get",
        url: "/api/updateFromSet_Meal/",
        params: params
      }).then(res => {
        if (res.data.success) {
          this.tableData = res.data.data;
          alert("修改成功");
          this.dialogForm2Visible = false;
        } else {
          alert("修改失败");
          this.dialogForm2Visible = false;
        }
      });
    },
    del_row(e) {
      if (confirm("确认要删除吗？")) {
        axios({
          method: "get",
          url: "/api/deleteFromSet_meal/",
          params: {
            id: e.id
          }
        }).then(res => {
          if (res.data.success) {
            this.tableData = res.data.data;
            alert("删除成功");
          }
        });
      }
    }
  }
};
``;
</script>

<style lang="stylus" scoped>
.elInput {
  width: 250px;
  margin-top: 10px;
}
</style>>




