<template>
  <div>
    <h1>OLT PON 口南向设备间连接关系 </h1>
    <el-table :data="tableData" style="width: 100%" height="550">
        <el-table-column
          v-for="(columnData,index) in tableInitData"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="180"
        ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableInitData: [
        ["olt_id", "链路名称"],
        ["olt_port", "是否trunk链路"],
        ["onu_id", "链路层次"],
        ["onu_type", "链路层次"],
        ["onu_manufacture", "链路层次"],
        ["onu_serial_number", "链路层次"],
        ["onu_ip", "链路层次"],
        ["Link_level", "链路层次"],
      ],
      tableData: [
        {
          onu_manufacture: "HW",
          onu_type: "MDU",
          onu_ip: "10.41.24.10",
          OS_id: "163943309",
          olt_id: "10.45.36.17",
          onu_serial_number: "BJ00000000",
          onu_id: "16",
          olt_port: "0/0/17/5"
        }
      ]
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/Device_interface_matching_south/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  }
};
</script>

<style>
</style>



