<template>
  <div class="selectThree" style="position:relative;align:left">
    <el-select style="width:100px" v-model="cityname" placeholder="city" filterable>
      <el-option value="天津" label="天津">天津</el-option>
    </el-select>

    <el-select
      v-model="firstname"
      clearable
      placeholder="department"
      @change="getsecond($event)"
      filterable
    >
      <el-option
        v-for="option in firstlist"
        :key="option"
        :label="option"
        :value="option"
      >{{option}}</el-option>
    </el-select>

    <el-select
      v-model="secondname"
      clearable
      placeholder="station"
      @change="getthird($event)"
      filterable
    >
      <el-option
        v-for="option in secondlist"
        :key="option"
        :label="option"
        :value="option"
      >{{option}}</el-option>
    </el-select>

    <el-select v-model="thirdname" clearable placeholder="olt" filterable>
      <el-option
        v-for="option in thirdlist"
        :key="option"
        :label="option"
        :value="option"
      >{{option}}</el-option>
    </el-select>

    <el-button type="primary" @click="childMethod">查询</el-button>
  </div>
</template>

<script>
// import Loading from '@/components/loading'
import axios from "axios";
export default {
  name: "selectThree",
  props: {
    search: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      firstlist: [],
      secondlist: [],
      thirdlist: [],
      showsecond: false,
      showthird: false,
      cityname: "天津",
      firstname: "",
      secondname: "",
      thirdname: ""
    };
  },
  watch: {
    firstname(val) {
      this.$store.dispatch("getNewDepartment", val);
    },
    secondname(val) {
      this.$store.dispatch("getNewStation", val);
    },
    thirdname(val) {
      this.$store.dispatch("getNewOLT", val);
    }
  },
  methods: {
    getfirst() {
      axios
        .get("/api/menus/department/")
        .then(response => {
          this.firstlist = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
      this.firstname = this.$store.getters.getDepartment;
      this.secondname = this.$store.getters.getStation;
      this.thirdname = this.$store.getters.getOLT;

      if (this.firstname) {
        axios
          .get(
            "/api/menus/" +
              this.$store.getters.getCity +
              "/" +
              this.firstname +
              "/"
          )
          .then(response => {
            this.secondlist = response.data.data;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
      if (this.secondname) {
        axios
          .get(
            "/api/menus/" +
              this.$store.getters.getCity +
              "/" +
              this.firstname +
              "/" +
              this.secondname +
              "/"
          )
          .then(response => {
            this.thirdlist = response.data.data;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
      // this.secondlist=this.$store.getters.getStationList

      // this.secondlist=this.$store.getters.getStationList
    },
    getsecond() {
      // this.showsecond=true
      this.secondname = "";
      this.thirdname = "";
      if (this.firstname == "") {
        this.secondlist = [];
        this.thirdlist = [];
      } else {
        axios
          .get(
            "/api/menus/" +
              this.$store.getters.getCity +
              "/" +
              this.firstname +
              "/"
          )
          .then(response => {
            this.secondlist = response.data.data;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    getthird() {
      // this.showthird=true
      this.thirdname = "";
      if (this.secondname == "") {
        this.thirdlist = [];
      } else {
        axios
          .get(
            "/api/menus/" +
              this.$store.getters.getCity +
              "/" +
              this.firstname +
              "/" +
              this.secondname +
              "/"
          )
          .then(response => {
            this.thirdlist = response.data.data;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
      // this.$store.dispatch('getNewOLTList',this.thirdlist)
    },
    childMethod() {
      if (this.search) {
        this.search();
      }
    }
    // search(){
    //     this.isLoading=true
    //     let _this=this
    //     setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
    // },
    // showform(_this){
    //     _this.isLoading=false;
    //     _this.msg="查询当前节点，添加动画效果"
    // }
  },
  created() {
    this.getfirst();
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



