<template>
  <div>
    <div class="selectTwo" style="position:relative;align:left">
      <el-select style="width:100px" v-model="cityname" placeholder="city" filterable>
        <el-option value="天津" label="天津">天津</el-option>
      </el-select>
      <el-select v-model="firstDep" clearable placeholder="department" filterable>
        <el-option
          v-for="option in firstlist"
          :key="option"
          :label="option"
          :value="option"
        >{{option}}</el-option>
      </el-select>
      <el-select v-model="meal1" placeholder="请选择套餐">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select v-model="secondDep" clearable placeholder="department" filterable>
        <el-option
          v-for="option in firstlist"
          :key="option"
          :label="option"
          :value="option"
        >{{option}}</el-option>
      </el-select>
      <el-select v-model="meal2" placeholder="请选择套餐">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button type="primary" @click="search">查询</el-button>
    </div>
    <div class="diffWrapper" v-loading="loading">
      <div class="LRWrapper">
        <div id="left-line-chart" class="line-chart"></div>
      </div>
      <div class="LRWrapper">
        <div id="right-line-chart" class="line-chart"></div>
      </div>
    </div>
  </div>
</template>

<script>
import selectMeal from "@/components/basic/selectMeal";
import echarts from "echarts";
import axios from "axios";

export default {
  name: "PonFlowSpeed",
  data() {
    return {
      cityname: '天津',
      firstDep: '',
      secondDep: '',
      meal1: '',
      meal2: '',
      firstlist: [],
      options: [
        {
          value: "200",
          label: "200"
        },
        {
          value: "300",
          label: "300"
        },
        {
          value: "500",
          label: "500"
        }
      ],
      list: [],
      loading: false,
      firstData: { avg: [], peak: [], avg_mean: -1, peak_mean: -1, date: [] },
      secondData: { avg: [], peak: [], avg_mean: -1, peak_mean: -1, date: [] },
      tableData: [],
    };
  },
  components: {
    selectMeal
  },
  methods: {
    initChart(domName) {
      let myChart = echarts.init(document.getElementById(domName));

      let categoryData = [];
      let valueData = [];
      let dataCount = 500;
      let data = generateData(dataCount);

      let option = {
        title: {
          text: echarts.format.addCommas(dataCount) + " Data",
          left: 10
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: false
            },
            saveAsImage: {
              pixelRatio: 2
            }
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          bottom: 90
        },
        dataZoom: [
          {
            type: "inside"
          },
          {
            type: "slider"
          }
        ],
        xAxis: {
          data: data.categoryData,
          silent: false,
          splitLine: {
            show: false
          },
          splitArea: {
            show: false
          }
        },
        yAxis: {
          splitArea: {
            show: false
          }
        },
        series: [
          {
            type: "bar",
            data: data.valueData,
            // Set `large` for large data amount
            large: true
          }
        ]
      };

      function generateData(count) {
        for (let i = 0; i < count; i++) {
          categoryData.push(i);
          valueData.push(Math.random() * 50);
        }

        return {
          categoryData: categoryData,
          valueData: valueData
        };
      }
      myChart.setOption(option);
    },
    search() {
      this.loading = true;
      let dep1 = this.firstDep;
      let meal1 = this.meal1;
      let dep2 = this.secondDep;
      let meal2 = this.meal2
      // console.log(`/api/${dep}/${meal}/history/`);
      Promise.all([
        axios({
          method: "get",
          url: `/api/${dep1}/${meal1}M/history/`
        }),
        axios({
          method: "get",
          url: `/api/${dep2}/${meal2}M/history/`
        })
      ])
        .then(res => {
          let result = res;
          console.log(result);
          let mealData_1 = res[0].data.data;
          this.firstData = {
            avg: mealData_1.avg,
            peak: mealData_1.peak,
            date: mealData_1.date,
            avg_mean: mealData_1.avg_mean,
            peak_mean: mealData_1.peak_mean
          };
          let mealData_2 = res[1].data.data;
          this.secondData = {
            avg: mealData_2.avg,
            peak: mealData_2.peak,
            date: mealData_2.date,
            avg_mean: mealData_2.avg_mean,
            peak_mean: mealData_2.peak_mean
          };
        })
        .then(() => {
          let title1 = this.firstDep + '   ' + this.meal1 + 'M'
          let title2 = this.secondDep + '   ' + this.meal2 + 'M'
          this.initLineChart(
            "left-line-chart",
            this.firstData,
            title1
          );
          this.initLineChart(
            "right-line-chart",
            this.secondData,
            title2
          );
          // let avg = {
          //   type: "平均",
          //   _200Meal: this._200Meal.avg_mean,
          //   _300Meal: this._300Meal.avg_mean,
          //   _500Meal: this._500Meal.avg_mean
          // };
          // let peak = {
          //   type: "峰值",
          //   _200Meal: this._200Meal.peak_mean,
          //   _300Meal: this._300Meal.peak_mean,
          //   _500Meal: this._500Meal.peak_mean
          // };
          // this.tableData = [];
          // this.tableData.push(avg);
          // this.tableData.push(peak);
          this.loading = false;
        });
    },
    initLineChart(domName, depData, name) {
      let myChart = echarts.init(document.getElementById(domName));
      let data = {};
      data.avg = depData.avg
      data.peak = depData.peak
      data.date = depData.date
      data.markPoint = [data.date[19],data.peak[19]]
      let option = {
        title: {
          text: name
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["200M", "300M", "500M"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: data.date
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "平均值",
            type: "line",
            data: depData.avg
          },
          {
            name: "峰值",
            type: "line",
            data: depData.peak,
            markPoint: {
              symbol: "pin",
              data: [
                {
                  name: "春节",
                  coord: data.markPoint
                }
              ],
              label: {
                show: "true",
                formatter: "春节"
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    },
    initBarChart(domName, _200Meal, _300Meal, _500Meal, type) {
      let myChart = echarts.init(document.getElementById(domName));
      let data = {};
      if (type === "avg") {
        data._200Meal = _200Meal.avg_mean;
        data._300Meal = _300Meal.avg_mean;
        data._500Meal = _500Meal.avg_mean;
        data.date = _200Meal.date;
        data.title = "均值";
      } else {
        data._200Meal = _200Meal.peak_mean;
        data._300Meal = _300Meal.peak_mean;
        data._500Meal = _500Meal.peak_mean;
        data.date = _200Meal.date;
        data.title = "峰值";
      }
      let option = {
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ["product", "200M", "300M", "500M"],
            [data.title, data._200Meal, data._300Meal, data._500Meal]
          ]
        },
        xAxis: { type: "category" },
        yAxis: {},
        series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }]
      };
      myChart.setOption(option);
    },
    getfirst() {
      axios
        .get("/api/menus/department/")
        .then(res => {
          console.log(res.data.data)
          this.firstlist = res.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  mounted() {
      this.getfirst()
  },

};
</script>

<style scoped lang="stylus">
.diffWrapper {
  display: flex;
  justify-content: space-around;
  height: 500px;
  width: 1200px;
  // background-color: #FF6666;
  padding: 30px;
  margin-top: 20px;

  .LRWrapper {
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    justify-content: space-around;

    // background-color: #FFFF00;
    .line-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }

    .bar-chart {
      width: 500px;
      height: 350px;
      // background-color: #006699;
    }
  }
}
</style>



