router.beforeEach((to, from, next) => {
  let islogin = store.state.islogin
  console.log(islogin, to.meta.requireAuth)
  if (to.meta.requireAuth) {
    if (islogin) {
      next()
    } else {
      next({
        path: '/login'
        // query: {redirect: to.fullPath}
      })
    }
  }
  else {
    next()
  }
})

