<template>
  <div>
    <h1>OLT 上联口北向设备间连接关系</h1>
    <el-table :data="tableData" style="width: 100%" height="550">
      <el-table-column label="链路基本信息">
        <el-table-column
          v-for="(columnData,index) in tableInitData1"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
      <el-table-column label="源端详细信息">
        <el-table-column
          v-for="(columnData,index) in tableInitData2"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
      <el-table-column label="宿端详细信息">
        <el-table-column
          v-for="(columnData,index) in tableInitData3"
          :key="index"
          :prop="columnData[0]"
          :label="columnData[1]"
          width="80"
        ></el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      tableInitData1: [
        ["Link_name", "链路名称"],
        ["is_trunk", "是否trunk链路"],
        ["Link_level", "链路层次"],
        ["Link_physical_or_trunk_bandwidth", "链路物理带宽(Mbps)"]
      ],
      tableInitData2: [
        ["Network_element_name", "网元名称"],
        ["Network_element_IP", "网元IP"],
        ["port_number", "端口号"],
        ["region", "区域"],
        ["Subnet", "子网"],
        ["Network_element_type", "网元类型"],
        ["Network_element_role", "网元角色"],
        ["Port_name", "端口名称"],
        ["Port_type", "端口类型"],
        ["Port_physical_bandwidth", "端口物理带宽(Mbps)"]
      ],
      tableInitData3: [
        ["Network_element_name1", "网元名称"],
        ["Network_element_IP1", "网元IP"],
        ["port_number1", "端口号"],
        ["region1", "区域"],
        ["Subnet1", "子网"],
        ["Network_element_type1", "网元类型"],
        ["Network_element_role", "网元角色"],
        ["Port_name1", "端口名称"],
        ["Port_type1", "端口类型"],
        ["Port_physical_bandwidth1", "端口物理带宽(Mbps)"]
      ],
      tableData: [
        {
          Subnet: "",
          Network_element_name: "",
          Network_element_type: "OLT",
          Network_element_role1: "",
          Port_type1: "",
          Network_element_type1: "LSW",
          port_number: "0/0/2/0",
          Network_element_IP1: "10.23.192.14",
          Port_physical_bandwidth: "",
          is_trunk: "NO",
          port_number1: "0/0/1/0",
          Network_element_name1: "",
          Network_element_role: "",
          Port_name: "",
          Port_type: "",
          Port_name1: "",
          Link_name: "1001",
          Link_level: "OLT-LSW",
          Subnet1: "",
          region: "",
          Network_element_IP: "10.45.36.16",
          Link_physical_or_trunk_bandwidth: "1",
          region1: "",
          Port_physical_bandwidth1: ""
        }
      ]
    };
  },
  mounted() {
    axios({
      method: "get",
      url: "/api/Link_reletion/"
    }).then(res => {
      if (res.data.success) {
        this.tableData = res.data.data;
      }
    });
  },
};
</script>

<style>
</style>



