<template>
  <div>
    <selectTwo :search="search"></selectTwo>
    <div class="block">
      <span class="demonstration">日期选择</span>
      <el-date-picker v-model="date" type="date" placeholder="选择日期"></el-date-picker>
    </div>
    <el-select v-model="selectUpDown" placeholder="请选择上行下行">
      <el-option v-for="item in selectUpDownOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-select v-model="selectPercent" placeholder="请选择百分比">
      <el-option v-for="item in selectPercentOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>

    <el-button type="primary" @click="export2Excel">出表</el-button>
    <el-button type="primary" @click="changeUp">{{changeUp_Down}}</el-button>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="user" label="用户分布"></el-table-column>
        <el-table-column prop="percent" label="占总流量比例"></el-table-column>
        <el-table-column prop="head_traffic" label="流量阈值（MB）"></el-table-column>
        <el-table-column prop="total_traffic" label="总流量（MB）"></el-table-column>
      </el-table>
    </div>
    <div class="chartWrapper" v-loading="loading">
      <div class="chartBorder">
        <h1 v-show="titleVisible">用户占比</h1>
        <div id="chart1-1" style="height:300px;width:350px"></div>
        <h1 v-show="titleVisible">流量占比</h1>
        <div id="chart1-2" style="height:300px;width:350px"></div>
      </div>
      <div class="chartBorder">
        <h1 v-show="titleVisible">用户占比</h1>
        <div id="chart2-1" style="height:300px;width:350px"></div>
        <h1 v-show="titleVisible">流量占比</h1>
        <div id="chart2-2" style="height:300px;width:350px"></div>
      </div>
      <div class="chartBorder">
        <h1 v-show="titleVisible">用户占比</h1>
        <div id="chart3-1" style="height:300px;width:350px"></div>
        <h1 v-show="titleVisible">流量占比</h1>
        <div id="chart3-2" style="height:300px;width:350px"></div>
      </div>
    </div>
  </div>
</template>

<script>
import selectTwo from "@/components/basic/selectTwo";
import echarts from "echarts";
import axios from "axios";
export default {
  name: "FlowDistribution",
  data() {
    return {
      msg: "",
      date: "",
      loading: false,
      changeUp_Down: "切换到上行流量",
      selectUpDown: "",
      titleVisible: false,
      selectUpDownOption: [
        {
          value: "up",
          label: "上行"
        },
        {
          value: "down",
          label: "下行"
        }
      ],
      selectPercentOption: [
        {
          value: "0.1%",
          label: "0.1%"
        },
        {
          value: "1%",
          label: "1%"
        },
        {
          value: "10%",
          label: "10%"
        },
      ],
      selectPercent: "",
      p_0_1_down: {},
      p_1_down: {},
      p_10_down: {},
      p_0_1_up: {},
      p_1_up: {},
      p_10_up: {},
      dataListDownload: [],
      tableData: [],
      tableDownData: [],
      tableUpData: [],
      chartData11: [],
      chartData12: [],
      chartData21: [],
      chartData22: [],
      chartData31: [],
      chartData32: []
    };
  },
  components: {
    selectTwo
  },
  methods: {
    search() {
      this.loading = true;
      let dep = this.$store.getters.getDepartment;
      let sta = this.$store.getters.getStation;
      Promise.all([
        axios({
          method: "get",
          url: "/api/aaa/top-k/",
          params: {
            department: dep,
            station: sta,
            k: 1000,
            date: 20190102
          }
        }),
        axios({
          method: "get",
          url: "/api/aaa/top-k/",
          params: {
            department: dep,
            station: sta,
            k: 100,
            date: 20190102
          }
        }),
        axios({
          method: "get",
          url: "/api/aaa/top-k/",
          params: {
            department: dep,
            station: sta,
            k: 10,
            date: 20190102
          }
        })
      ])
        .then(result => {
          console.log(result);
          let data0 = result[0].data.data;
          this.p_0_1_down = {
            user: "0.1%",
            total_traffic: data0.downstream.total_traffic,
            percent:
              (
                data0.downstream.head_traffic / data0.downstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data0.downstream.head_traffic
          };
          this.p_0_1_up = {
            user: "0.1%",
            total_traffic: data0.upstream.total_traffic,
            percent:
              (
                data0.upstream.head_traffic / data0.upstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data0.upstream.head_traffic
          };

          let data1 = result[1].data.data;
          this.p_1_down = {
            user: "1%",
            total_traffic: data1.downstream.total_traffic,
            percent:
              (
                data1.downstream.head_traffic / data1.downstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data1.downstream.head_traffic
          };

          this.p_1_up = {
            user: "1%",
            total_traffic: data1.upstream.total_traffic,
            percent:
              (
                data1.upstream.head_traffic / data1.upstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data1.upstream.head_traffic
          };

          let data2 = result[2].data.data;
          this.p_10_down = {
            user: "10%",
            total_traffic: data2.downstream.total_traffic,
            percent:
              (
                data2.downstream.head_traffic / data2.downstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data2.downstream.head_traffic
          };
          this.p_10_up = {
            user: "10%",
            total_traffic: data2.upstream.total_traffic,
            percent:
              (
                data2.upstream.head_traffic / data2.upstream.total_traffic
              ).toFixed(2) *
                100 +
              "%",
            head_traffic: data2.upstream.head_traffic
          };

          this.tableDownData = [];
          this.tableDownData.push(this.p_0_1_down);
          this.tableDownData.push(this.p_1_down);
          this.tableDownData.push(this.p_10_down);
          this.tableData = this.tableDownData;

          this.tableUpData = [];
          this.tableUpData.push(this.p_0_1_up);
          this.tableUpData.push(this.p_1_up);
          this.tableUpData.push(this.p_10_up);
          this.dataListDownload = data2.downstream.list;
          console.log(this.dataListDownload);
        })
        .then(() => {
          this.loading = false;
          this.titleVisible = true;
          this.chartData1 = [
            { value: 0.001, name: "0.1%" },
            { value: 0.999, name: "其余流量" }
          ];
          this.chartData2 = [
            { value: this.p_0_1_down.head_traffic, name: (this.p_0_1_down.head_traffic / this.p_0_1_down.total_traffic).toFixed(2)*100 + '%' },
            {
              value: this.p_0_1_down.total_traffic - this.p_0_1_down.head_traffic,
              name: ((this.p_0_1_down.total_traffic - this.p_0_1_down.head_traffic) / this.p_0_1_down.total_traffic ).toFixed(2)*100 + '%'
            }
          ];
          this.initPieChart("chart1-1", this.chartData1);
          this.initPieChart("chart1-2", this.chartData2);

          this.chartData3 = [
            { value: 0.01, name: "1%" },
            { value: 0.99, name: "其余流量" }
          ];
          this.chartData4 = [
            { value: this.p_1_down.head_traffic, name: (this.p_1_down.head_traffic / this.p_1_down.total_traffic).toFixed(2)*100 + '%' },
            {
              value: this.p_1_down.total_traffic - this.p_1_down.head_traffic,
              name: ((this.p_1_down.total_traffic - this.p_1_down.head_traffic) / this.p_1_down.total_traffic).toFixed(2)*100 + '%'
            }
          ];
          this.initPieChart("chart2-1", this.chartData3);
          this.initPieChart("chart2-2", this.chartData4);

          this.chartData5 = [
            { value: 0.1, name: "10%" },
            { value: 0.9, name: "其余流量" }
          ];
          this.chartData6 = [
            { value: this.p_10_down.head_traffic, name: (this.p_10_down.head_traffic / this.p_10_down.total_traffic).toFixed(2) * 100 + '%' },
            {
              value: this.p_10_down.total_traffic - this.p_10_down.head_traffic,
              name: ((this.p_10_down.total_traffic - this.p_10_down.head_traffic) / this.p_10_down.total_traffic).toFixed(2) *100 + '%'
            }
          ];
          this.initPieChart("chart3-1", this.chartData5);
          this.initPieChart("chart3-2", this.chartData6);
        });
    },
    changeUp() {
      console.log("输出", this.tableUpData, this.tableDownData);
      if (this.changeUp_Down === "切换到上行流量") {
        this.changeUp_Down = "切换到下行流量";
        this.tableData = this.tableUpData;
        this.chartData2 = [
          { value: this.p_0_1_up.head_traffic, name: "0.1%" },
          {
            value: this.p_0_1_up.total_traffic - this.p_0_1_up.head_traffic,
            name: "其余流量"
          }
        ];
        this.chartData4 = [
          { value: this.p_1_up.head_traffic, name: "1%" },
          {
            value: this.p_1_up.total_traffic - this.p_1_up.head_traffic,
            name: "其余流量"
          }
        ];
        this.chartData6 = [
          { value: this.p_10_up.head_traffic, name: "10%" },
          {
            value: this.p_10_up.total_traffic - this.p_10_up.head_traffic,
            name: "其余流量"
          }
        ];
        this.initPieChart("chart1-2", this.chartData2);
        this.initPieChart("chart2-2", this.chartData4);
        this.initPieChart("chart3-2", this.chartData6);
      } else {
        this.changeUp_Down = "切换到上行流量";
        this.tableData = this.tableDownData;
        this.chartData2 = [
          { value: this.p_0_1_down.head_traffic, name: "0.1%" },
          {
            value: this.p_0_1_down.total_traffic - this.p_0_1_down.head_traffic,
            name: "其余流量"
          }
        ];
        this.chartData4 = [
          { value: this.p_1_down.head_traffic, name: "1%" },
          {
            value: this.p_1_down.total_traffic - this.p_1_down.head_traffic,
            name: "其余流量"
          }
        ];
        this.chartData6 = [
          { value: this.p_10_down.head_traffic, name: "10%" },
          {
            value: this.p_10_down.total_traffic - this.p_10_down.head_traffic,
            name: "其余流量"
          }
        ];
        this.initPieChart("chart1-2", this.chartData2);
        this.initPieChart("chart2-2", this.chartData4);
        this.initPieChart("chart3-2", this.chartData6);
      }
    },
    initPieChart(domName, data) {
      // 传入 dom 名称，以及 data object。
      let myChart = echarts.init(document.getElementById(domName));

      let option = {
        color: ["#E74C3C",'#AED6F1'],
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          x: "left",
          data: data
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      };
      myChart.setOption(option);
      window.addEventListener("resize", function() {
        myChart.resize();
      });
    },
    // 导出 excel 的函数
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export2Excel() {
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("../../../vendor/Export2Excel");
        const tHeader = [
          "用户 ID",
          "分公司",
          "OLT 名称",
          "PON 板号",
          "PON 口号",
          "ONU 型号",
          "用户套餐",
          "上行流量(MB)",
          "下行流量(MB)",
          "在线时长(s)"
        ];
        const filterVal = [
          "id",
          "department_name",
          "olt_name",
          "pon_board",
          "pon_port",
          "onu",
          "speed",
          "upstream",
          "downstream",
          "online_time"
        ];
        const list = this.goodsItems;
        const data = this.formatJson(filterVal, this.dataListDownload);
        export_json_to_excel(tHeader, data, "历史查询");
      });
    }
  }
};
</script>

<style scoped>
.chartWrapper {
  display: flex;
  flex: 1;
  justify-content: space-between;
  margin-top: 20px;
}
.chartBorder {
  border-right: 5px solid #3498DB;
  /* border-left: 2px solid #3498DB */
}
h1 {
  text-align: center;
  color: #3498DB
}
</style>



