<template>
  <div class="ponCount">
    <selectThree :search="search"></selectThree>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
    <div id="pon_rate_chart1" style="height:500px;margin-top:20px"></div>
  </div>
</template>

<script>
import Loading from "@/components/loading";
import selectThree from "@/components/basic/selectThree";
import echarts from "echarts";
import axios from "axios";
export default {
  name: "ponCount",
  data() {
    return {
      msg: "",
      isLoading: false,
      poncount: "",
      ponstatpie: "",
      ponstatbar: ""
    };
  },
  components: {
    Loading,
    selectThree
  },
  computed: {
    city() {
      return this.$store.getters.getCity;
    },
    department() {
      return this.$store.getters.getDepartment;
    },
    station() {
      return this.$store.getters.getStation;
    },
    olt() {
      return this.$store.getters.getOLT;
    }
  },
  methods: {
    initChart(chart, datapie, databar) {
      let myChart = echarts.init(document.getElementById(chart));
      myChart.clear();
      let option = {
        color: [
          "#31C5C0",
          "#A0CE3A",
          "#1E9BD1",
          "#0F347B",
          "#585247",
          "#7F6AAD",
          "#009D85",
          "#CD8162",
          "#CDC9A5",
          "#CD6600",
          "#DAA520",
          "#6B8E23",
          "#9F79EE"
        ],
        backgroundColor: "#fff",
        tooltip: {
          trigger: "axis"
        },
        toolbox: {
          feature: {
            saveAsImage: { show: true, backgroundColor: "#fff" }
          }
        },
        grid: { top: "10%", left: "5%", right: "50%" },
        legend: { orient: "vertical", x: "right", y: "center", data: datapie },
        calculable: true,
        xAxis: [
          {
            type: "category",
            data: databar[0]
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "端口数"
          }
        ],
        series: [
          {
            name: "端口数",
            type: "bar",
            barMaxWidth: "100",
            data: databar[1]
          },
          {
            name: "数量",
            type: "pie",
            radius: ["20%", "35%"],
            center: ["75%", "50%"],
            label: {
              normal: {
                formatter: " {b|{b}}\n {hr|}\n {c} {per|{d}%}  ",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                rich: {
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 14,
                    lineHeight: 20,
                    align: "center"
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              }
            },
            data: datapie
          }
        ]
      };
      myChart.setOption(option);
      window.onresize = function() {
        myChart.resize();
      };
    },
    _createPonStat(data) {
      this.ponstatpie = [];
      this.ponstatbar = [];
      this.ponstatpie = [
        {
          name: "空闲端口",
          value: data["idle_port_count"]
        },
        {
          name: "占用端口",
          value: data["used_port_count"]
        }
      ];
      let tempx = ["空闲端口", "占用端口"];
      let tempy = [data["idle_port_count"], data["used_port_count"]];
      this.ponstatbar.push(tempx);
      this.ponstatbar.push(tempy);
      //待添加
    },
    search() {
      this.isLoading = true;
      let that = this
      //let _this=this
      // axios
      //   .get("/api/pon-port-count/", {
      //     params: {
      //       city: this.city,
      //       department: this.department
      //         ? this.department
      //           ? this.department.slice(0, -3) + "通信分公司"
      //           : this.department
      //         : this.department,
      //       station: this.station,
      //       olt: this.olt
      //     }
      //   })
      //   .then(response => {
      //     console.log(response.data)
      //     this.poncount = response.data.data["pon_port_count"];
      //   })
      //   .catch(function(error) {
      //     console.log(error);
      //   });
      axios
        .get("/api/pon-port-stat-/", {
          params: {
            city: this.city,
            department: this.department
              ? this.department.slice(0, -3) + "通信分公司"
              : this.department,
            station: this.station,
            olt: this.olt
          }
        })
        .then(response => {
          console.log(response.data)
          this.isLoading = false;
          if (response.data.data!=null){
          this._createPonStat(response.data.data);
          this.initChart("pon_rate_chart1", this.ponstatpie, this.ponstatbar);
          }
          else{
            this.$message.error('数据为空');
          }
        })
        .catch(
          function(error) {
            that.$message.error('请求错误');
            that.isLoading = false;
          }
        );
      //setTimeout(function(){ _this.isLoading=false;_this.msg="查询当前节点，添加动画效果"}, 3000);
    }
  },
  created() {
    // this.getfirst();
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



